import { useContext } from 'react';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Types
import { BaseFieldType, Field, Page, PageState } from '../types';

export const usePageState = <
  I extends Record<string, unknown>,
  N extends BaseFieldType,
>(): PageState => {
  const context = useContext(FormGeneratorContext);

  const dirtyPages = [] as string[];
  const pageErrors = [] as string[];

  context.options.pages.forEach((page: Page) => {
    if (page.name === context.currentPage) {
      return;
    }
    context.fieldMap.forEach((field: Field<I, N>) => {
      if (page.name === field.page && context.dirtyFields[field.name]) {
        dirtyPages.push(page.name);
      }
      if (page.name === field.page && context.errors[field.name]) {
        pageErrors.push(page.name);
      }
    });
  });

  return {
    pages: context.options.pages,
    errorPages: pageErrors,
    dirtyPages: dirtyPages,
  };
};

export default usePageState;
