import { useContext } from 'react';
import { getFieldDefault } from '../../utils';

// Context
import { FormGeneratorContext } from '../../FormGenerator';

// Types
import { BaseFieldType, Field as FieldType } from '../../types';

const Field = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
}: {
  field: FieldType<I, N>;
}): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const FieldType = field && context.options.fieldTypes[field.type];
  if (!FieldType) {
    return <></>;
  }
  return (
    <FieldType
      field={field}
      name={field.name}
      defaultValue={getFieldDefault(
        field,
        context.options.fieldTypeDefaultValues,
      )}
    />
  );
};

export default Field;
