import { ElementType, useContext } from 'react';

// Context
import { FormGeneratorContext } from '../FormGenerator';

const SaveDraft = (): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const SaveDraftButton = context.options.components.saveDraft as ElementType;

  return (
    <SaveDraftButton
      sizeType="small"
      styleType="standard"
      data-testid="FormGenerator__Save_Draft_Button"
      className="sb-form-generator_toolbar-action sb-form-generator_toolbar-action_save_draft"
      isFetching={context.actionRunning == 'saveDraft'}
      disabled={context.actionRunning}
      onClick={() => context.setSavingDraft(true)}
    >
      {context.options.saveDraftButton ?? 'Save'}
    </SaveDraftButton>
  );
};

export default SaveDraft;
