import { useContext, useEffect, useMemo, useState } from 'react';
import classNames from 'classnames';
import get from 'lodash/get';

// Components
import FormSelectField from '../../FormSelectField';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import useRequired from '../Hooks/useRequired';
import useWatchFields from '../Hooks/useWatchFields';
import useUpdateFieldValue from '../Hooks/useUpdateFieldValue';
import useFieldReadOnly from '../Hooks/useFieldReadOnly';
import useFieldDisabled from '../Hooks/useFieldDisabled';

// Styles
import './Field.scss';

// Types
import { BaseFieldType, FieldContext, FieldProps, Options } from '../types';

const SelectField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  loadName,
  parentName,
  defaultValue,
  index,
  isDeleting = false,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const fieldsWatch = useWatchFields(context.values, field.watch, index);
  const isVisible = context.utils.isVisible(field, index);
  const isDisabled = useFieldDisabled(field, index);
  const isReadOnly = useFieldReadOnly(field, index);

  const [fieldsContext, setFieldsContext] = useState<FieldContext>({
    value: '',
    type: 'input',
  });
  const menuPortalTarget = context.utils.getMenuPortalTarget(field);
  const selectClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_select',
        {
          'sb-form-generator_field_hidden': !isVisible,
          'sb-form_generator_select-inline': field.layout === 'inline',
        },
        field.class,
      ),
    [field.class, field.layout, isVisible],
  );

  const isRequired = useRequired(field, context.values);

  const [isFetching, setIsFetching] = useState(false);
  const [selectOptions, setSelectOptions] = useState<Options | undefined>(
    field?.options as Options,
  );

  useEffect(() => {
    field?.options &&
      field?.options.length > 0 &&
      setSelectOptions(field?.options as Options);
  }, [field?.options]);

  useEffect((): (() => void) => {
    let isMounted = true;
    const loadData = get(context.loadData, loadName || field.name);
    if (loadData && !isDeleting) {
      setIsFetching(true);
      loadData(
        context.values,
        context.values
          ? get(context.values, `${parentName}.[${index}]`)
          : undefined,
        fieldsContext,
      ).then((options: Options) => {
        if (isMounted) {
          setIsFetching(false);
          setSelectOptions(options);
        }
      });
    }
    return () => (isMounted = false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [field.name, fieldsWatch, fieldsContext.value]);

  useUpdateFieldValue(
    name,
    loadName || field.name,
    fieldsWatch,
    context.values,
    context.transport,
    undefined,
    isDeleting,
  );

  const defaultValueProps = {
    ...(defaultValue && { defaultValue: defaultValue }),
  };

  const id =
    typeof index !== 'undefined'
      ? `${context.formId}_${index}_${field.name}`
      : `${context.formId}_${field.name}`;

  return (
    <FormSelectField
      labelFor={id}
      key={name}
      fieldName={name}
      labelText={field.label}
      tooltip={field.tooltip}
      helper={field.description}
      required={isRequired && !isReadOnly}
      className={selectClassNames}
      testMetadata={{
        wrapper: {
          'data-testid': `FormGenerator__SelectField_Container_${field.name}`,
        },
        input: {
          'data-testid': `FormGenerator__SelectField_${field.name}`,
        },
      }}
      selectProps={{
        useMenuPortal: field.layout !== 'inline',
        menuPortalTarget: menuPortalTarget,
        closeMenuOnScroll: (e) => {
          if (!field.closeMenuOnScroll) return;
          return (
            e.target instanceof Element &&
            !e.target.classList?.contains('select__menu-list')
          );
        },
        menuShouldBlockScroll: field.blockScrollWhenOpen,
        menuPlacement: 'auto',
        isFetching: isFetching,
        inputId: id,
        options: selectOptions,
        isSearchable: true,
        blurInputOnSelect: false,
        className: 'sb-form-generator_select-input',
        placeholder: <div>{field.placeholder || 'Select...'}</div>,
        isDisabled: isDisabled,
        isClearable: 'clearable' in field ? field.clearable : true,
        onInputChange: (value) => {
          setFieldsContext({ value: value, type: 'input' });
        },
      }}
      fixedHeightFeedback={context.options.display.feedback.reserveSpace}
      customGetErrorMessage={field.customGetErrorMessage}
      customIsErrorValidation={field.customIsErrorValidation}
      creatable={field.creatable}
      async={field.async}
      readOnly={isReadOnly}
      {...defaultValueProps}
    />
  );
};

export default SelectField;
