import classNames from 'classnames';
import { Control, Controller, RegisterOptions, Path } from 'react-hook-form';

// Components
import FormField, { FormFieldProps } from '../FormField';
import Input, { InputProps } from '../Input';
import FormReadOnlyFieldTextValue from '../FormReadOnlyFieldTextValue';

// Types
import { TestMetadata } from 'types';

export interface FormTextFieldProps<I extends Record<string, unknown>> {
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Display name for field */
  helper?: string;
  /** Class name */
  className?: string;
  /** Mark field required (*) */
  required?: boolean;
  /** Min length of characters required */
  minLength?: number;
  /** Max length of characters required */
  maxLength?: number;
  /** Rules to pass down to control */
  rules?: RegisterOptions;
  /** Additional input props for Input, based on the Input Component */
  inputProps?: Partial<InputProps>;
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    input?: TestMetadata;
  };
  defaultValue?: unknown;
}

const FormTextField = <I extends Record<string, unknown>>({
  fieldName,
  labelText,
  tooltip,
  required,
  rules,
  inputProps,
  helper,
  testMetadata,
  minLength,
  maxLength,
  readOnly,
  truncate,
  defaultValue,
  ...otherProps
}: FormTextFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  return (
    <FormField<I>
      labelText={labelText}
      fieldName={fieldName}
      tooltip={tooltip}
      required={required}
      readOnly={readOnly}
      helper={helper}
      {...testMetadata?.wrapper}
      control={(formMethods, props) => (
        <Controller
          name={fieldName as string}
          defaultValue={defaultValue !== undefined ? defaultValue : ''}
          control={formMethods.control as Control<Record<string, unknown>>}
          rules={{
            required: required ? `${labelText} is required.` : undefined,
            minLength: minLength || undefined,
            maxLength: maxLength || undefined,
            ...rules,
          }}
          render={({ field: { onChange, onBlur, value, name } }) => {
            if (readOnly) {
              return (
                <FormReadOnlyFieldTextValue
                  value={value as string}
                  testMetadata={testMetadata?.input}
                  truncate={truncate}
                />
              );
            }

            return (
              <Input
                {...props}
                name={name as string}
                width="auto"
                data-testid={`FormTextField_${String(fieldName)}`}
                {...inputProps}
                {...testMetadata?.input}
                className={classNames(inputProps?.className, props.className)}
                onChange={onChange}
                onBlur={onBlur}
                value={value as string}
              />
            );
          }}
        />
      )}
      {...otherProps}
    />
  );
};

export default FormTextField;
