import { ElementType, useContext } from 'react';

// Context
import { FormGeneratorContext } from '../FormGenerator';

const Submit = (): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const SubmitButton = context.options.components.submit as ElementType;

  return (
    <SubmitButton
      type="submit"
      sizeType="small"
      data-testid="FormGenerator__Save_Button"
      className="sb-form-generator_toolbar-action sb-form-generator_toolbar-action_submit"
      isFetching={context.actionRunning == 'submit'}
      disabled={context.actionRunning || !context.isSubmittable}
      onClick={() => context.setSavingDraft(false)}
    >
      {context.options.saveButton}
    </SubmitButton>
  );
};

export default Submit;
