import { FC } from 'react';
import classNames from 'classnames';

// Components
import Modal from '../Modal';

// Styles
import './ConfirmationModal.scss';

// Types
import { MessageType, TestMetadata } from 'types';

export interface ConfirmationModalProps {
  /** Set text for modal header */
  title: string;
  /** Set text for modal content */
  text: string;
  /** Cancel Button text */
  cancelButtonText?: string;
  /** Accept Button text */
  acceptButtonText?: string;
  /** Callback fired when the component requests to be closed */
  onClose?: () => void;
  /** Callback fired when the component requests to be confirm */
  onConfirm?: () => void;
  /** Modal type */
  messageType?: MessageType;
  /** Modal should overlay */
  bodyOverlay?: 'blackout' | 'whiteout';
  /** Hide/show the close button */
  hideClose?: boolean;
  /** Show smaller buttons */
  smallButtons?: boolean;
  /** Props to pass to the secondary button */
  secondaryButtonProps?: Record<string, unknown>;
  /** Props to pass to the primary button */
  primaryButtonProps?: Record<string, unknown>;
  /** Test IDs for sections of ConfirmationModal */
  testMetadata?: {
    confirmationModal?: TestMetadata;
    primaryButton?: TestMetadata;
    secondaryButton?: TestMetadata;
    content?: TestMetadata;
  };
}

const ConfirmationModal: FC<ConfirmationModalProps> = ({
  title,
  text,
  cancelButtonText = 'No',
  acceptButtonText = 'Yes',
  onClose,
  onConfirm,
  messageType,
  testMetadata = {
    primaryButton: { 'data-testid': 'accept-modal-btn' },
  },
  bodyOverlay,
  hideClose,
  smallButtons,
  secondaryButtonProps = {},
  primaryButtonProps = {},
}) => {
  return (
    <Modal
      headerText={title}
      onClose={onClose}
      primaryButtonAction={onConfirm}
      secondaryButtonAction={onClose}
      primaryButton={acceptButtonText}
      secondaryButton={cancelButtonText}
      hideClose={hideClose}
      smallButtons={smallButtons}
      headerAlign="center"
      footerAlign="center"
      dialogClassName="sb-confirmation-modal__dialog"
      bodyOverlay={bodyOverlay}
      className={classNames({
        'sb-confirmation-modal__success': messageType === 'success',
        'sb-confirmation-modal__warning': messageType === 'warning',
        'sb-confirmation-modal__error': messageType === 'error',
      })}
      primaryButtonProps={{
        className: 'sb-confirmation-modal-button',
        ...primaryButtonProps,
        ...testMetadata?.primaryButton,
      }}
      secondaryButtonProps={{
        className: 'sb-confirmation-modal-button',
        ...secondaryButtonProps,
        ...testMetadata?.secondaryButton,
      }}
      {...testMetadata?.confirmationModal}
    >
      <div
        className="sb-confirmation-modal__content"
        {...testMetadata?.content}
      >
        {messageType && (
          <i
            aria-label={messageType}
            className={classNames('sb-confirmation-modal-icon', 'fas', {
              'fa-check-circle': messageType === 'success',
              'fa-exclamation-circle': messageType === 'warning',
              'fa-times-circle': messageType === 'error',
            })}
          />
        )}
        {text}
      </div>
    </Modal>
  );
};

export default ConfirmationModal;
