import {
  Controller,
  FieldErrors,
  Path,
  PathValue,
  RegisterOptions,
} from 'react-hook-form';
import classNames from 'classnames';
import findKey from 'lodash/findKey';
import get from 'lodash/get';
import pick from 'lodash/pick';
import has from 'lodash/has';

// Components
import Checkbox from '../Checkbox';
import Checkboxes from '../Checkboxes';
import FormField, { FormFieldProps } from '../FormField';
import FormReadOnlyFieldListValue from '../FormReadOnlyFieldListValue';
import FormReadOnlyFieldTextValue from '../FormReadOnlyFieldTextValue';

// Styles
import './FormCheckboxField.scss';

// Types
import { TestMetadata, FormCheckboxFieldOption } from '../types';
import { HTMLProps } from 'react';

type GetOptionsErrors = <I extends Record<string, unknown>>(
  errors: FieldErrors<I>,
  options: FormCheckboxFieldOption[],
) => Pick<FieldErrors<I>, string>;

const getOptionsErrors: GetOptionsErrors = (errors, options) =>
  pick(
    errors,
    options.map((option) => String(option.id)),
  );

type IsOneChecked = <I extends Record<string, unknown>>(
  values: I,
  options: FormCheckboxFieldOption[],
) => boolean;

const isOneChecked: IsOneChecked = (values, options) =>
  options.some((option) => {
    return values[option.id] === true;
  });

export interface FormCheckboxFieldProps<I extends Record<string, unknown>> {
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Mark field required (*) */
  required?: boolean;
  /** Check box options such name and id */
  options?: FormCheckboxFieldOption[];
  /** Horizontal orientation of the checkboxes */
  horizontal?: boolean;
  /** Rules to pass down to control */
  rules?: RegisterOptions;
  /** Additional input props for Input, based on the Input Component */
  inputProps?: Partial<HTMLProps<HTMLInputElement> & TestMetadata>;
  defaultValue?: unknown;
  /** Sets helper */
  helper?: string;
  /** Class name */
  className?: string;
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    input?: TestMetadata;
  };
}

const FormCheckboxField = <I extends Record<string, unknown>>({
  fieldName,
  labelText,
  tooltip,
  required,
  options,
  horizontal,
  rules,
  inputProps,
  helper,
  className,
  testMetadata,
  defaultValue,
  readOnly,
  ...otherProps
}: FormCheckboxFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  if (!options || options.length === 0) {
    return <></>;
  }
  const formCheckboxClassNames = classNames('sb-checkbox-field', className);

  return (
    <FormField<I>
      labelText={labelText}
      tooltip={tooltip}
      fieldName={fieldName}
      required={required}
      helper={helper}
      className={formCheckboxClassNames}
      readOnly={readOnly}
      customGetErrorMessage={(errors, values) => {
        const optionsErrors = getOptionsErrors(errors, options);
        return (
          (!isOneChecked(values, options) ||
            Object.keys(optionsErrors).length === options.length) &&
          (get(errors, `${String(fieldName)}.message`) ||
            get(optionsErrors, `${findKey(optionsErrors, 'message')}.message`))
        );
      }}
      customIsErrorValidation={(errors, values) => {
        const optionsErrors = getOptionsErrors(errors, options);
        return (
          (!isOneChecked(values, options) ||
            Object.keys(optionsErrors).length === options.length) &&
          options.some((option) => {
            return has(optionsErrors, option.id);
          })
        );
      }}
      control={(formMethods, props) => (
        <>
          {!isOneChecked(formMethods.getValues(), options) && readOnly && (
            <FormReadOnlyFieldTextValue
              testMetadata={{
                'data-testid': testMetadata?.input?.['data-testid']
                  ? `${testMetadata.input['data-testid']}_Empty`
                  : undefined,
              }}
            />
          )}
          <Checkboxes
            horizontal={horizontal || readOnly}
            className={classNames({
              'sb-checkbox-field_read-only': readOnly,
            })}
          >
            {options?.map((option) => (
              <Controller
                key={option.id}
                name={String(option.id) as Path<I>}
                control={formMethods.control}
                defaultValue={
                  (defaultValue || option.value) as PathValue<I, Path<I>>
                }
                rules={{
                  validate: {
                    required: () => {
                      const optionsNames = options.map(
                        (option) => String(option.id) as unknown as Path<I>,
                      );

                      return !required
                        ? undefined
                        : isOneChecked(formMethods.getValues(), options)
                        ? (formMethods.clearErrors(optionsNames), undefined)
                        : `${labelText} is required.`;
                    },
                  },
                  ...rules,
                }}
                render={({ field: { onChange, value, ...rest } }) => {
                  const checkboxTestMetadata: TestMetadata = {
                    'data-qeid': testMetadata?.input?.['data-qeid']
                      ? `${testMetadata.input['data-qeid']}_${option.id}`
                      : undefined,
                    'data-testid': testMetadata?.input?.['data-testid']
                      ? `${testMetadata.input['data-testid']}_${option.id}`
                      : undefined,
                  };

                  if (readOnly) {
                    if (!value) {
                      return <></>;
                    }

                    return (
                      <FormReadOnlyFieldListValue
                        values={[option.label as string]}
                        testMetadata={checkboxTestMetadata}
                      />
                    );
                  }

                  return (
                    <Checkbox
                      option={option}
                      disabled={option.readonly}
                      onChange={() => {
                        onChange(!value);
                      }}
                      checked={value as boolean}
                      inputProps={{
                        ...checkboxTestMetadata,
                        ...rest,
                        ...inputProps,
                      }}
                      {...props}
                    />
                  );
                }}
              />
            ))}
          </Checkboxes>
        </>
      )}
      {...testMetadata?.wrapper}
      {...otherProps}
    />
  );
};

export default FormCheckboxField;
