import { useContext, useMemo } from 'react';
import classNames from 'classnames';

// Components
import Typography from '../../Typography';
import FormField from '../../FormField';
import { FileUploader } from '../../FileUploader';
import { Controller, Path, PathValue } from 'react-hook-form';
import TooltipTemplate from '../../TooltipTemplate';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import useRequired from '../Hooks/useRequired';
import useFieldReadOnly from '../Hooks/useFieldReadOnly';
import useFieldDisabled from '../Hooks/useFieldDisabled';

// Types
import { BaseFieldType, FieldProps } from '../types';

// Styles
import './UploadField.scss';
import { SBFile } from 'types';

const UploadField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  defaultValue,
  index,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const isVisible = context.utils.isVisible(field, index);
  const isDisabled = useFieldDisabled(field, index);
  const isReadOnly = useFieldReadOnly(field, index);
  const inputClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_upload',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );

  const isRequired = useRequired(field, context.values);

  const defaultValueProps = {
    ...(defaultValue && {
      defaultValue: defaultValue as PathValue<I, Path<I>>,
    }),
  };

  const id =
    index !== undefined
      ? `${context.formId}_${index}_${field.name}`
      : `${context.formId}_${field.name}`;

  return (
    <div
      className={inputClassNames}
      data-testid={`FormGenerator__UploadField_Container_${field.name}`}
    >
      {field.label && (
        <div className="sb-form-generator_upload-header">
          <Typography
            variant="T30"
            className="sb-form-generator_label sb-form-generator_upload-label"
          >
            {field.label}
            {field.tooltip && (
              <TooltipTemplate
                tooltipContent={field.tooltip}
                size="small"
                testMetadata={{
                  icon: {
                    'data-testid': `FormGenerator__UploadField_Tooltip_${field.name}`,
                  },
                }}
              />
            )}
            {isRequired && !isReadOnly && (
              <span className="sb-form-generator_upload-required">
                *Required
              </span>
            )}
          </Typography>
          {field.description && !isReadOnly && (
            <Typography
              variant="T15"
              className="sb-form-generator_description sb-form-generator_upload-description"
            >
              {field.description}
            </Typography>
          )}
        </div>
      )}
      <div className="sb-form-generator_upload-field-container">
        <FormField
          fieldName={name}
          className="sb-form-generator_upload-field"
          data-testid={`FormGenerator__UploadField_Field_${field.name}`}
          readOnly={isReadOnly}
          control={(formMethods) => (
            <Controller
              {...defaultValueProps}
              name={name}
              control={formMethods.control}
              render={({ field: { value, onChange } }) => (
                <FileUploader
                  id={id}
                  testMetadata={{
                    input: {
                      'data-testid': `FormGenerator__UploadField_${field.name}`,
                    },
                    button: {
                      'data-testid': `FormGenerator__UploadField_Button_${field.name}`,
                    },
                    delete: {
                      'data-testid': `FormGenerator__UploadField_Delete_${field.name}`,
                    },
                  }}
                  displayFiles={
                    field?.layout === 'single' || !field.multiple
                      ? 'string'
                      : 'list'
                  }
                  value={value ? Array.from(value as SBFile[]) : []}
                  isDisabled={isDisabled}
                  isReadOnly={isReadOnly}
                  multiple={field.multiple}
                  description={field.subdescription}
                  accept={field.accept}
                  maxSize={field.fileSize}
                  handleFileUpload={async (files, rawFiles) => {
                    // FileList is not a directly creatable interface but we can mimic it with a DataTransfer object
                    const dt = new DataTransfer();
                    const curr = value as any;

                    // We can't append to a FileList either, so re-add current files in the new object
                    if (curr && curr.length > 0) {
                      for (let i = 0; i < curr.length; i++) {
                        const file = curr[i];
                        dt.items.add(file);
                      }
                    }
                    // Add the new files (phew!)
                    for (let i = 0; i < rawFiles.length; i++) {
                      const file = rawFiles[i];
                      if (
                        files.find(
                          (currentFile) =>
                            file.name === currentFile.name &&
                            !currentFile.error,
                        )
                      ) {
                        dt.items.add(file);
                      }
                    }
                    onChange(dt.files);
                    return;
                  }}
                />
              )}
            />
          )}
          fixedHeightFeedback={context.options.display.feedback.reserveSpace}
          customGetErrorMessage={field.customGetErrorMessage}
          customIsErrorValidation={field.customIsErrorValidation}
        />
      </div>
    </div>
  );
};

export default UploadField;
