import { Control, Controller, RegisterOptions, Path } from 'react-hook-form';
import classNames from 'classnames';

// Components
import FormField, { FormFieldProps } from '../FormField';
import { Radio, RadioButtons, RadioProps } from '../RadioButtons';
import FormReadOnlyFieldTextValue from '../FormReadOnlyFieldTextValue';

// Styles
import './FormRadioField.scss';

// Types
import { FormCheckboxFieldOption, TestMetadata } from '../types';

export interface FormRadioFieldProps<I extends Record<string, unknown>> {
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Sets helper */
  helper?: string;
  /** Mark field required (*) */
  required?: boolean;
  /** Rules to pass down to control */
  rules?: RegisterOptions;
  /** Props for the input tag */
  inputProps?: Partial<RadioProps['inputProps']>;
  /** Class name */
  className?: string;
  // Horizontal layout
  horizontal?: boolean;
  /** Check box options such name and id */
  options?: FormCheckboxFieldOption[];
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    input?: TestMetadata;
  };
  // Default value
  defaultValue?: unknown;
}

const FormRadioField = <I extends Record<string, unknown>>({
  fieldName,
  required,
  rules,
  inputProps,
  labelText,
  tooltip,
  helper,
  className,
  horizontal,
  options,
  testMetadata,
  readOnly,
  defaultValue,
  truncate,
  ...otherProps
}: FormRadioFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  const formRadioClassNames = classNames('sb-radio-field', className);
  return (
    <FormField<I>
      labelText={labelText}
      tooltip={tooltip}
      fieldName={fieldName}
      required={required}
      helper={helper}
      className={formRadioClassNames}
      readOnly={readOnly}
      control={(formMethods, props) => (
        <Controller
          name={fieldName as string}
          defaultValue={defaultValue !== undefined ? defaultValue : null}
          control={formMethods.control as Control<Record<string, unknown>>}
          rules={{
            required: required ? `${labelText} is required.` : undefined,
            ...rules,
          }}
          render={({ field: { onChange, value } }) => {
            if (readOnly) {
              const selectedOption: FormCheckboxFieldOption | undefined =
                options?.find((option) => option.value === value);

              return (
                <FormReadOnlyFieldTextValue
                  value={
                    typeof selectedOption?.label === 'string'
                      ? selectedOption?.label
                      : undefined
                  }
                  testMetadata={testMetadata?.input}
                  truncate={truncate}
                />
              );
            }

            return (
              <RadioButtons horizontal={horizontal}>
                {options?.map((option) => (
                  <Radio
                    key={option.id}
                    option={option}
                    onChange={onChange}
                    checked={option.value === value}
                    testMetadata={{
                      'data-testid': testMetadata?.input?.['data-testid']
                        ? `${testMetadata.input['data-testid']}_${option.id}`
                        : undefined,
                    }}
                    inputProps={{
                      name: String(fieldName),
                      disabled: option.readonly,
                      value: String(option.value),
                      ...inputProps,
                    }}
                    {...props}
                  />
                ))}
              </RadioButtons>
            );
          }}
        />
      )}
      {...testMetadata?.wrapper}
      {...otherProps}
    />
  );
};

export default FormRadioField;
