import { useState, useContext, useMemo } from 'react';
import classNames from 'classnames';
import ReactMarkdown from 'react-markdown';
import ReactMde from 'react-mde';
import { Controller, Path, PathValue } from 'react-hook-form';

// Components
import FormField from '../../FormField';
import FormReadOnlyFieldTextValue from '../../FormReadOnlyFieldTextValue';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import useRequired from '../Hooks/useRequired';
import useFieldReadOnly from '../Hooks/useFieldReadOnly';
import useFieldDisabled from '../Hooks/useFieldDisabled';

// Types
import { BaseFieldType, FieldProps } from '../types';

const MarkdownField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  defaultValue,
  index,
}: FieldProps<I, N>): JSX.Element => {
  const [selectedTab, setSelectedTab] = useState<'write' | 'preview'>('write');

  const context = useContext(FormGeneratorContext);
  const isVisible = context.utils.isVisible(field, index);
  const isDisabled = useFieldDisabled(field, index);
  const isReadOnly = useFieldReadOnly(field, index);

  const isRequired = useRequired(field, context.values);

  const markdownClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_markdown',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );

  const defaultValueProps = {
    ...(defaultValue && {
      defaultValue: defaultValue as PathValue<I, Path<I>>,
    }),
  };

  return (
    <FormField
      labelText={field.label}
      tooltip={field.tooltip}
      fieldName={name}
      required={isRequired && !isReadOnly}
      helper={field.description}
      className={markdownClassNames}
      readOnly={isReadOnly}
      data-testid={`FormGenerator__MarkdownField_Container_${field.name}`}
      control={(formMethods, props) => (
        <Controller
          name={name}
          control={formMethods.control}
          {...defaultValueProps}
          render={({ field: { value, onChange } }) => {
            if (isReadOnly) {
              return (
                <FormReadOnlyFieldTextValue
                  value={value as string}
                  testMetadata={{
                    'data-testid': `FormGenerator__MarkdownField_Textarea_${field.name}`,
                  }}
                  truncate={field.truncate}
                />
              );
            }

            return (
              <ReactMde
                classes={{
                  textArea: props.isError
                    ? 'sb-form-generator_markdown_error'
                    : '',
                }}
                value={value as string}
                onChange={onChange}
                selectedTab={selectedTab}
                onTabChange={setSelectedTab}
                generateMarkdownPreview={(markdown) =>
                  Promise.resolve(<ReactMarkdown>{markdown}</ReactMarkdown>)
                }
                readOnly={isDisabled}
                childProps={{
                  textArea: {
                    id: field.name,
                    'data-testid': `FormGenerator__MarkdownField_Textarea_${field.name}`,
                    placeholder: field.placeholder,
                  },
                }}
                {...props}
              />
            );
          }}
        />
      )}
      fixedHeightFeedback={context.options.display.feedback.reserveSpace}
      customGetErrorMessage={field.customGetErrorMessage}
      customIsErrorValidation={field.customIsErrorValidation}
    />
  );
};

export default MarkdownField;
