import { useContext, useMemo } from 'react';
import classNames from 'classnames';

// Components
import FormField from '../../FormField';
import Input from '../../Input';
import FormReadOnlyFieldTextValue from '../../FormReadOnlyFieldTextValue';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import useRequired from '../Hooks/useRequired';
import useFieldReadOnly from '../Hooks/useFieldReadOnly';
import useFieldDisabled from '../Hooks/useFieldDisabled';

// Styles
import './Field.scss';

// Types
import { BaseFieldType, FieldProps } from '../types';
import { Control, Controller } from 'react-hook-form';

const NumberField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  defaultValue,
  index,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const isVisible = context.utils.isVisible(field, index);
  const isDisabled = useFieldDisabled(field, index);
  const isReadOnly = useFieldReadOnly(field, index);
  const numberClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_number',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );
  const isRequired = useRequired(field, context.values);

  return (
    <FormField
      key={name}
      fieldName={name}
      labelText={field.label}
      tooltip={field.tooltip}
      helper={field.description}
      required={isRequired && !isReadOnly}
      className={numberClassNames}
      data-testid={`FormGenerator__NumberField_Container_${field.name}`}
      control={(formMethods, props) => (
        <Controller
          name={name as string}
          defaultValue={defaultValue !== undefined ? defaultValue : null}
          control={formMethods.control as Control<Record<string, unknown>>}
          rules={{
            required: isRequired ? `${field.label} is required.` : undefined,
            min: field.min || undefined,
            max: field.max || undefined,
          }}
          render={({ field: { onChange, onBlur, value, name } }) => {
            if (isReadOnly) {
              return (
                <FormReadOnlyFieldTextValue
                  value={value as string}
                  testMetadata={{
                    'data-testid': `FormGenerator__NumberField_${field.name}`,
                  }}
                  truncate={field.truncate}
                />
              );
            }

            return (
              <Input
                data-testid={`FormGenerator__NumberField_${field.name}`}
                type="number"
                name={name}
                min={field.min}
                max={field.max}
                step={field.step}
                readOnly={isDisabled}
                placeholder={field.placeholder}
                width="auto"
                onChange={onChange}
                onBlur={onBlur}
                value={value as string}
                {...props}
              />
            );
          }}
        />
      )}
      fixedHeightFeedback={context.options.display.feedback.reserveSpace}
      customGetErrorMessage={field.customGetErrorMessage}
      customIsErrorValidation={field.customIsErrorValidation}
      readOnly={isReadOnly}
    />
  );
};

export default NumberField;
