import { useContext, useEffect, useMemo, useState } from 'react';
import classNames from 'classnames';
import { Control, Controller } from 'react-hook-form';

// Components
import FormField from '../../FormField';
import { MultiSelectPopout } from '../../MultiSelect';
import FormReadOnlyFieldListValue from '../../FormReadOnlyFieldListValue';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import useRequired from '../Hooks/useRequired';
import useWatchFields from '../Hooks/useWatchFields';
import useFieldReadOnly from '../Hooks/useFieldReadOnly';
import useFieldDisabled from '../Hooks/useFieldDisabled';

// Styles
import './Field.scss';

// Types
import { FieldProps, Options, Option, BaseFieldType } from '../types';

const MultiSelectPopoutField = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>({
  field,
  index,
  defaultValue,
  isDeleting = false,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const fieldsWatch = useWatchFields(context.values, field.watch, index);
  const isVisible = context.utils.isVisible(field, index);
  const isDisabled = useFieldDisabled(field, index);
  const isReadOnly = useFieldReadOnly(field, index);
  const menuPortalTarget = context.utils.getMenuPortalTarget(field);

  const multiSelectPopoutClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_multiselect-popout',
        {
          'sb-form-generator_field_hidden': !isVisible,
          'sb-form_generator_multiselect-inline': field.layout === 'inline',
        },
        field.class,
      ),
    [field.class, field.layout, isVisible],
  );

  const isRequired = useRequired(field, context.values);
  const [isFetching, setIsFetching] = useState(false);
  const [selectOptions, setSelectOptions] = useState<Options | undefined>(
    field?.options as Options,
  );

  useEffect((): (() => void) => {
    let isMounted = true;
    if (context.loadData?.[field.name] && !isDeleting) {
      setIsFetching(true);
      context.loadData?.[field.name](context.values).then(
        (options: Options) => {
          if (isMounted) {
            setIsFetching(false);
            setSelectOptions(options);
          }
        },
      );
    }
    return () => (isMounted = false);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [field.name, fieldsWatch]);

  return (
    <FormField
      helper={field.description}
      key={field.name}
      fieldName={field.name}
      labelText={field.label}
      labelFor={`${context.formId}_${field.name}`}
      tooltip={field.tooltip}
      required={isRequired && !isReadOnly}
      className={multiSelectPopoutClassNames}
      readOnly={isReadOnly}
      data-testid={`FormGenerator__MultiSelectPopoutField_Container_${field.name}`}
      control={(formMethods, props) => (
        <Controller
          name={field.name as string}
          control={formMethods.control as Control<Record<string, unknown>>}
          defaultValue={defaultValue !== undefined ? defaultValue : null}
          render={({ field: { onChange, onBlur, value, name } }) => {
            if (isReadOnly) {
              return (
                <FormReadOnlyFieldListValue
                  values={
                    value
                      ? (value as Option[]).map((v: Option) => v.label)
                      : undefined
                  }
                  testMetadata={{
                    'data-testid': `FormGenerator__MultiSelectPopoutField_${field.name}`,
                  }}
                />
              );
            }

            return (
              <MultiSelectPopout
                customClear={false}
                summary={
                  field?.mode === 'summary' || field?.mode === 'summaryGrouped'
                }
                groupSelected={
                  field?.mode === 'grouped' || field?.mode === 'summaryGrouped'
                }
                required={isRequired && !isReadOnly}
                deta-testid={`FormGenerator__MultiSelectPopoutField_${field.name}`}
                isFetching={isFetching}
                options={selectOptions as Option[]}
                placeholder={<div>{field.placeholder || 'Select...'}</div>}
                inputId={`${context.formId}_${field.name}`}
                isDisabled={isDisabled}
                isClearable={'clearable' in field ? field.clearable : true}
                onChange={onChange}
                onBlur={onBlur}
                value={value as Option[]}
                name={name}
                selectProps={{
                  inputId: `${context.formId}_${field.name}`,
                  className: 'sb-form-generator_multiselectpopout-input',
                }}
                useMenuPortal={field.layout !== 'inline'}
                menuPortalTarget={menuPortalTarget}
                closeMenuOnScroll={field.closeMenuOnScroll}
                menuShouldBlockScroll={field.blockScrollWhenOpen}
                showSelectAllOption={field.showSelectAllOption}
                {...props}
              />
            );
          }}
        />
      )}
      fixedHeightFeedback={context.options.display.feedback.reserveSpace}
      customGetErrorMessage={field.customGetErrorMessage}
      customIsErrorValidation={field.customIsErrorValidation}
    />
  );
};

export default MultiSelectPopoutField;
