import { Children, FC, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './Checkboxes.scss';

// Types
import { TestMetadata } from 'types';

export interface CheckboxesProps {
  /** Custom class name(s) for component container */
  className?: string;
  /** Optional title prop */
  title?: string;
  /** Custom classname for list item */
  listItemClassName?: string;
  /** Horizontal orientation of the checkboxes */
  horizontal?: boolean;
  // Test IDs for sections of CheckBoxes
  testMetadata?: TestMetadata;
}

const Checkboxes: FC<CheckboxesProps> = ({
  className,
  listItemClassName,
  children,
  title,
  horizontal,
  testMetadata,
  ...otherProps
}) => {
  const checkboxesClassNames = useMemo(
    () =>
      classNames(
        'sb-checkboxes__list',
        { 'sb-checkboxes__list--horizontal': horizontal },
        className,
      ),
    [className, horizontal],
  );

  const liClassNames = useMemo(
    () => classNames('sb-checkboxes__list-item', listItemClassName),
    [listItemClassName],
  );

  return (
    <div className="sb-checkboxes" {...testMetadata}>
      <div className="sb-checkboxes__title">{title}</div>
      <ul {...otherProps} className={checkboxesClassNames}>
        {Children.map(children, (child) => (
          <li className={liClassNames}>{child}</li>
        ))}
      </ul>
    </div>
  );
};

export default Checkboxes;
