import { useFormContext } from 'react-hook-form';

// Components
import Input from '../../Input';

// Context
import { BaseFieldType, FieldProps } from '../types';

const HiddenField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  defaultValue,
}: FieldProps<I, N>): JSX.Element => {
  const { ...methods } = useFormContext<I>();

  // Passing undefined to defaultValue appears to clear certain fields. Using this method, we avoid passing the prop at all if it's not needed
  const defaultValueProps = {
    ...(defaultValue && { defaultValue: defaultValue as string }),
  };

  const { ref, ...registerProps } = methods.register(name);
  return (
    <Input
      type="hidden"
      inputRef={ref}
      {...registerProps}
      data-testid={`FormGenerator__HiddenField_${field.name}`}
      {...defaultValueProps}
    />
  );
};

export default HiddenField;
