import { useContext } from 'react';

// Components
import ButtonGroup from '../../ButtonGroup';

// Types
import { ToolbarActionType } from '../types';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Styles
import './FormToolbar.scss';

const FormToolbar = (): JSX.Element => {
  const context = useContext(FormGeneratorContext);

  const toolbarActionNames = context.options.toolbar?.actions ?? [];
  const customActions: ToolbarActionType =
    context.options.customToolbarActions ?? {};

  return (
    <ButtonGroup
      className="sb-form-generator_toolbar"
      testMetadata={{
        'data-testid': 'FormGenerator__Component_FormToolbar',
      }}
      {...context.testMetadata?.toolbar}
    >
      {toolbarActionNames.map((actionName: string, index: number) => {
        const ToolbarAction = customActions[actionName];

        if (!ToolbarAction) {
          return null;
        }

        if (actionName === 'cancel') {
          return !context.hideCleanCancel ? (
            <ToolbarAction key={index} />
          ) : null;
        }

        if (actionName === 'save') {
          return !context.formLocked ? <ToolbarAction key={index} /> : null;
        }

        if (actionName === 'submit') {
          return !context.formLocked ? <ToolbarAction key={index} /> : null;
        }

        if (actionName === 'edit') {
          return context.formLocked && context.formUnlockable ? (
            <ToolbarAction key={index} />
          ) : null;
        }

        return <ToolbarAction key={index} />;
      })}
    </ButtonGroup>
  );
};

export default FormToolbar;
