import get from 'lodash/get';
import isEqual from 'lodash/isEqual';
import isValid from 'date-fns/isValid';
import isBefore from 'date-fns/isBefore';
import isAfter from 'date-fns/isAfter';
import parse from 'date-fns/parse';
import dateEqual from 'date-fns/isEqual';

// Types
import { ButtonProps } from 'Button';
import { GridAreaProps } from 'Grid';
import {
  BaseFieldType,
  Breakpoint,
  CalculatedSize,
  ConditionalAction,
  ConditionalLogic,
  CustomFieldDefaultValueType,
  DefaultValueType,
  EditableAction,
  Field,
  FieldValidator,
  FormGeneratorSize,
  FormOptions,
  GridMode,
  Operator,
  PaginationSchema,
  Rule,
  RuleDateConfig,
  SubmitContext,
  ToolbarPlacement,
  ToolbarSchema,
  FormFieldValue,
  FormSchema,
  EnabledAction,
} from './types';
import { SelectOption } from 'types';
import { DeepPartial } from 'react-hook-form';

export const setVisibility = (
  action: ConditionalAction,
  logic: ConditionalLogic,
  matches: number,
  numRules: number,
): boolean => {
  if (logic === 'all') {
    if (matches === numRules) {
      return action === 'show' ? true : false;
    }
  }
  if (logic === 'any') {
    if (matches > 0) {
      return action === 'show' ? true : false;
    }
  }
  return action === 'show' ? false : true;
};

export const setEditable = (
  action: EditableAction,
  logic: ConditionalLogic,
  matches: number,
  numRules: number,
): boolean => {
  if (logic === 'all') {
    if (matches === numRules) {
      return action === 'editable' ? true : false;
    }
  }
  if (logic === 'any') {
    if (matches > 0) {
      return action === 'editable' ? true : false;
    }
  }
  return action === 'editable' ? false : true;
};

export const setEnabled = (
  action: EnabledAction,
  logic: ConditionalLogic,
  matches: number,
  numRules: number,
): boolean => {
  if (logic === 'all') {
    if (matches === numRules) {
      return action === 'enabled';
    }
  }
  if (logic === 'any') {
    if (matches > 0) {
      return action === 'enabled';
    }
  }
  return action !== 'enabled';
};

export const processRule = (
  operator: Operator,
  value: unknown,
  fieldValue: unknown,
  compareValue?: string | number | SelectOption | SelectOption[],
  date?: RuleDateConfig,
): boolean => {
  if (operator === 'empty') {
    if (!value) {
      return true;
    }
    if (Array.isArray(value)) {
      return value?.length === 0;
    } else {
      return value === '';
    }
  }

  if (operator === 'notempty') {
    if (value) {
      if (Array.isArray(value)) {
        return value?.length > 0;
      } else {
        return value !== '';
      }
    }
    return false;
  }

  // Equal
  if (operator === 'equal') {
    return isEqual(value, compareValue);
  }

  if (operator === 'notequal') {
    return !isEqual(value, compareValue);
  }

  // Contains
  if (
    operator === 'contain' &&
    value &&
    typeof value === 'string' &&
    typeof compareValue === 'string'
  ) {
    return value.includes(compareValue);
  }

  if (
    operator === 'notcontain' &&
    value &&
    typeof value === 'string' &&
    typeof compareValue === 'string'
  ) {
    return !value.includes(compareValue);
  }

  // Greater
  if (operator === '>' && value && compareValue) {
    if (typeof value === 'number' && value > compareValue) {
      return true;
    }
    if (
      typeof value === 'string' &&
      typeof compareValue === 'string' &&
      date?.format
    ) {
      const maybeDate = parse(value, date.format, new Date());
      const compareDate = parse(compareValue, date.format, new Date());
      if (
        isValid(maybeDate) &&
        isValid(compareDate) &&
        isAfter(maybeDate, compareDate)
      ) {
        return true;
      }
    }
    return false;
  }

  if (operator === '>=' && value && compareValue) {
    if (typeof value === 'number' && value >= compareValue) {
      return true;
    }
    if (
      typeof value === 'string' &&
      typeof compareValue === 'string' &&
      date?.format
    ) {
      const maybeDate = parse(value, date.format, new Date());
      const compareDate = parse(compareValue, date.format, new Date());
      if (
        (isValid(maybeDate) &&
          isValid(compareDate) &&
          isAfter(maybeDate, compareDate)) ||
        dateEqual(maybeDate, compareDate)
      ) {
        return true;
      }
    }
    return false;
  }

  // Less
  if (operator === '<' && value && compareValue) {
    if (typeof value === 'number' && value < compareValue) {
      return true;
    }
    if (
      typeof value === 'string' &&
      typeof compareValue === 'string' &&
      date?.format
    ) {
      const maybeDate = parse(value, date.format, new Date());
      const compareDate = parse(compareValue, date.format, new Date());
      if (
        isValid(maybeDate) &&
        isValid(compareDate) &&
        isBefore(maybeDate, compareDate)
      ) {
        return true;
      }
    }
    return false;
  }

  if (operator === '<=' && value && compareValue) {
    if (typeof value === 'number' && value <= compareValue) {
      return true;
    }
    if (
      typeof value === 'string' &&
      typeof compareValue === 'string' &&
      date?.format
    ) {
      const maybeDate = parse(value, date.format, new Date());
      const compareDate = parse(compareValue, date.format, new Date());
      if (
        (isValid(maybeDate) &&
          isValid(compareDate) &&
          isBefore(maybeDate, compareDate)) ||
        dateEqual(maybeDate, compareDate)
      ) {
        return true;
      }
    }
    return false;
  }
  return false;
};

export const fieldEditable = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  formValues: I,
  index?: number,
): boolean => {
  if (field.editable) {
    const { action, logic, rules } = field.editable;
    const currentFieldValue = get(formValues, field.name);

    let matches = 0;
    rules.forEach((rule: Rule) => {
      const { field: fieldName, operator, value, date } = rule;

      const name =
        index !== undefined
          ? fieldName.replace('[i]', `[${index}]`)
          : fieldName;
      const formValue = get(formValues, name);

      if (formValue === undefined) {
        return;
      }

      if (processRule(operator, formValue, currentFieldValue, value, date)) {
        matches += 1;
      }
    });
    return setEditable(action, logic, matches, rules.length);
  }
  return true;
};

export const fieldEnabled = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  formValues: I,
  index?: number,
): boolean => {
  if (field.enabled) {
    const { action, logic, rules } = field.enabled;
    const currentFieldValue = get(formValues, field.name);

    let matches = 0;
    rules.forEach((rule: Rule) => {
      const { field: fieldName, operator, value, date } = rule;

      const name =
        index !== undefined
          ? fieldName.replace('[i]', `[${index}]`)
          : fieldName;
      const formValue = get(formValues, name);

      if (formValue === undefined) {
        return;
      }

      if (processRule(operator, formValue, currentFieldValue, value, date)) {
        matches += 1;
      }
    });
    return setEnabled(action, logic, matches, rules.length);
  }
  return true;
};

export const fieldVisible = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  options: FormOptions<I, N>,
  formValues: I,
  breakpoint: Breakpoint | '',
  index?: number,
): boolean => {
  if ((typeof field.size === 'number' || field.size) && breakpoint) {
    const fieldSize =
      typeof field.size === 'number'
        ? field.size
        : breakpoint in field.size
        ? field.size[breakpoint]
        : options.defaultFieldSize[breakpoint];
    if (Number(fieldSize) === 0) {
      return false;
    }
  }
  return processFieldRules(field, formValues, index);
};

export const processBreakpointRules = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  currentPage: string | boolean,
  breakpoint: string,
  options: FormOptions<I, N>,
): boolean => {
  const paginationHidden = options?.form?.pagination?.hideBreakpoints?.some(
    (bp) => bp === breakpoint,
  );
  if (options.pages) {
    // Don't show field if (not on current page) AND (pagination visible)
    if (field.page !== currentPage && !paginationHidden) {
      return false;
    }
  }
  return true;
};

export const processFieldRules = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  formValues: I,
  index?: number,
): boolean => {
  if (field.visibility) {
    const { action, logic, rules } = field.visibility;
    const currentFieldValue = get(formValues, field.name);

    let matches = 0;
    rules.forEach((rule: Rule) => {
      const { field: fieldName, operator, value, date } = rule;
      const name =
        index !== undefined
          ? fieldName.replace('[i]', `[${index}]`)
          : fieldName;
      const formValue = get(formValues, name);
      if (formValue === undefined) {
        return;
      }

      if (processRule(operator, formValue, currentFieldValue, value, date)) {
        matches += 1;
      }
    });
    return setVisibility(action, logic, matches, rules.length);
  }
  return true;
};

export const generateField = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  options: FormOptions<I, N>,
): boolean => {
  if (options.visibleFields) {
    const visibleField = options.visibleFields.includes(field.name);
    const visibleSection =
      field.type === 'section' && field.fields
        ? field.fields.some((field: Field<I, N>) => {
            return generateField(field, options);
          })
        : false;
    return visibleField || visibleSection;
  }
  return true;
};

export const showField = <I extends Record<string, unknown>, N = BaseFieldType>(
  field: Field<I, N>,
  currentPage: string | boolean,
  options: FormOptions<I, N>,
  formValues: I,
  gridMode: GridMode,
  screenSize?: FormGeneratorSize,
  index?: number,
): boolean => {
  const breakpoint =
    gridMode === 'slim'
      ? 'slim'
      : screenSize
      ? getBreakpoint(screenSize, gridMode === 'container')
      : '';
  if (!processBreakpointRules(field, currentPage, breakpoint, options)) {
    return false;
  }
  return fieldVisible(field, options, formValues, breakpoint, index);
};

export const getFieldSize = (
  slim: number,
  small: number,
  medium: number,
  large: number,
): CalculatedSize => {
  return {
    slim: slim,
    small: small,
    medium: medium,
    large: large,
  };
};

export const calculateFieldSizes = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  options: FormOptions<I, N>,
  gridMode: GridMode,
  screenSize?: FormGeneratorSize,
): GridAreaProps => {
  let fieldSize;

  if (field.size) {
    fieldSize =
      typeof field.size === 'number'
        ? getFieldSize(field.size, field.size, field.size, field.size)
        : getFieldSize(
            field.size.slim ? field.size.slim : options.defaultFieldSize.slim,
            field.size.small
              ? field.size.small
              : options.defaultFieldSize.small,
            field.size.medium
              ? field.size.medium
              : options.defaultFieldSize.medium,
            field.size.large
              ? field.size.large
              : options.defaultFieldSize.large,
          );
  } else {
    fieldSize = options.defaultFieldSize;
  }

  if (gridMode === 'viewport') {
    return fieldSize;
  } else if (gridMode === 'container' && screenSize) {
    const viewportBreakpoint = getBreakpoint(screenSize, false);
    const containerBreakpoint = getBreakpoint(screenSize, true);
    const modifier =
      viewportBreakpoint !== 'slim' && containerBreakpoint === 'slim' ? 2 : 1;
    return getFieldSize(
      fieldSize[containerBreakpoint],
      fieldSize[containerBreakpoint] * modifier,
      fieldSize[containerBreakpoint] * modifier,
      fieldSize[containerBreakpoint] * modifier,
    );
  } else if (gridMode === 'slim') {
    return getFieldSize(
      fieldSize['slim'],
      fieldSize['slim'] * 2,
      fieldSize['slim'] * 2,
      fieldSize['slim'] * 2,
    );
  }

  return fieldSize;
};

export const getFieldDefault = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  customValues?: CustomFieldDefaultValueType,
): DefaultValueType => {
  if (customValues?.[field.type as string]) {
    return customValues[field.type as string];
  } else {
    switch (field.type) {
      case 'input':
      case 'inputmask':
      case 'markdown':
      case 'hidden':
      case 'textarea':
      case 'number':
      case 'readonly':
        return '';
      case 'checkbox':
      case 'toggle':
        return false;
      case 'select':
      case 'multiselect':
      case 'multiselectpopout':
      case 'date':
      case 'radio':
        return null;
      case 'toggleswitch':
        return (field.options as ReadonlyArray<ButtonProps>)[0].value
          ? ((field.options as ReadonlyArray<ButtonProps>)[0].value as string)
          : 0;
      case 'tablerepeater': {
        if (field.rowRequired && field.fields) {
          const defaultRow = field.fields.reduce((prev, curr) => {
            return {
              ...prev,
              [curr.name]: getFieldDefault(curr, customValues),
            };
          }, {});
          return [defaultRow];
        } else {
          return [];
        }
      }
      case 'upload':
      case 'filemanager': {
        return [];
      }
      default:
        return undefined;
    }
  }
};

export const getBreakpoint = (
  screenSize: FormGeneratorSize,
  useContainer?: boolean,
): Breakpoint => {
  const size = useContainer ? screenSize.container : screenSize.viewport;
  if (size.innerWidth >= 1240) {
    return 'large';
  } else if (size.innerWidth >= 1024) {
    return 'medium';
  } else if (size.innerWidth >= 768) {
    return 'small';
  } else {
    return 'slim';
  }
};

export const getFieldContext = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  formValues: I,
  refMap: Record<string, Field<I, N>>,
): Pick<SubmitContext<I>, 'visibleFields' | 'enabledFields'> => {
  const context = {
    visibleFields: {},
    enabledFields: {},
  } as Pick<SubmitContext<I>, 'visibleFields' | 'enabledFields'>;
  for (const [key] of Object.entries(formValues)) {
    const field = refMap[key];

    if (field) {
      if (
        field.type === 'section' ||
        field.type === 'hidden' ||
        field.detached
      ) {
        continue;
      } else if (field.type === 'tablerepeater') {
        const visibleChildFields = [] as Record<string, boolean>[];
        const enabledChildFields = [] as Record<string, boolean>[];
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const childFields = formValues[key] as Record<string, any>[];

        childFields.forEach((item, index) => {
          const visibleFields = {} as Record<string, boolean>;
          const enabledFields = {} as Record<string, boolean>;
          for (const [childKey] of Object.entries(item)) {
            const childField = refMap[childKey];
            if (childField) {
              visibleFields[childKey] = processFieldRules(
                childField,
                formValues,
                index,
              );
              enabledFields[childKey] =
                !childField.disabled &&
                fieldEnabled(childField, formValues, index);
            }
          }
          visibleChildFields.push(visibleFields);
          enabledChildFields.push(enabledFields);
        });

        context.visibleFields[key] = visibleChildFields;
        context.enabledFields[key] = enabledChildFields;
      } else {
        context.visibleFields[key] = processFieldRules(field, formValues, 0);
        context.enabledFields[key] =
          !field.disabled && fieldEnabled(field, formValues, 0);
      }
    }
  }
  return context;
};

export const toolbarPlacement = (
  breakpoint: string,
  toolbarPlacement: ToolbarSchema['location'],
): ToolbarPlacement => {
  if (typeof toolbarPlacement === 'string') {
    return toolbarPlacement;
  }

  if (toolbarPlacement?.[breakpoint]) {
    return toolbarPlacement[breakpoint];
  }

  return 'none';
};

export const pagesVisible = (
  breakpoint: Breakpoint,
  gridMode: GridMode,
  screenSize: FormGeneratorSize,
  pagination?: PaginationSchema,
): boolean => {
  if (pagination) {
    if (!pagination.hideBreakpoints.includes(breakpoint)) {
      return true;
    }
    return false;
  } else {
    return gridMode === 'slim'
      ? false
      : screenSize
      ? getBreakpoint(screenSize, gridMode === 'container') !== 'slim'
      : false;
  }
};

export const fieldRequired = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  values: DeepPartial<I>,
): boolean => {
  if (!field.validator) {
    return false;
  }

  return field.validator.some((validator: FieldValidator | string) => {
    if (typeof validator === 'string' && validator === 'required') {
      return true;
    }
    if (typeof validator === 'object' && validator.type === 'required') {
      const dependentFieldValue = validator?.meta?.dependentFieldValue;
      const dependentFieldNull = validator?.meta?.dependentFieldNull;
      const dependentField = validator?.meta?.dependentField;
      if (dependentFieldValue && dependentField) {
        if (
          isDependantFieldValueEqual(
            values[dependentField] as FormFieldValue,
            dependentFieldValue,
          )
        ) {
          return true;
        } else {
          return false;
        }
      } else if (dependentFieldNull && dependentField) {
        if (
          isDependantFieldValueNull(values[dependentField] as FormFieldValue)
        ) {
          return true;
        } else {
          return false;
        }
      }
      return true;
    }
    return false;
  });
};

export const isDependantFieldValueEqual = (
  field?: FormFieldValue,
  value?: FormFieldValue,
): boolean => {
  if (!field || !value) {
    return false;
  }

  if (typeof field === 'object' && 'value' in field) {
    return field?.value === value;
  }

  if (Array.isArray(field)) {
    return field.some((item) => item.value === value);
  }

  // Other types.
  return field === value;
};

export const isDependantFieldValueNull = (field?: FormFieldValue): boolean => {
  if (field !== null && typeof field === 'object' && 'value' in field) {
    return !field?.value;
  }

  if (Array.isArray(field)) {
    return !field.some((item) => item.value);
  }

  // Other types.
  return !field;
};

export const parseFormSchema = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  form: FormSchema<I, N> | string,
): FormSchema<I, N> => {
  try {
    return typeof form === 'string' ? JSON.parse(form) : form;
  } catch (e) {
    throw Error('FormGenerator: Unable to load form config');
  }
};
