import Input from '../../../Input';

// Context
import { useFormContext } from 'react-hook-form';

const BulkField = <I extends Record<string, unknown>>({
  name,
}: {
  name: string;
}): JSX.Element => {
  const { ...methods } = useFormContext<I>();
  const { ref, ...registerProps } = methods.register(`${name}[0].bulk` as any);
  return (
    <Input
      type="hidden"
      inputRef={ref}
      {...registerProps}
      data-testid="FormGenerator__HiddenField_bulk"
      defaultValue="true"
    />
  );
};

export default BulkField;
