import { useCallback, useContext } from 'react';
import { FormGeneratorContext } from '../FormGenerator';
import Button from '../../Button';

const Edit = (): JSX.Element => {
  const context = useContext(FormGeneratorContext);

  const onEdit = useCallback(
    (e) => {
      e.preventDefault();
      context?.setFormLocked(false);
    },
    [context],
  );

  return (
    <Button
      sizeType="small"
      data-testid="FormGenerator__Edit_Button"
      onClick={onEdit}
    >
      Edit
    </Button>
  );
};

export default Edit;
