import { useContext, useMemo } from 'react';
import classNames from 'classnames';
import { Control, Controller, Path, PathValue } from 'react-hook-form';

// Components
import Button from '../../Button';
import FormField from '../../FormField';
import ToggleSwitch from '../../ToggleSwitch';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import useRequired from '../Hooks/useRequired';

// Styles
import './Field.scss';

// Types
import { BaseFieldType, FieldProps, ToggleSwitchOption } from '../types';

const ToggleSwitchField = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>({
  field,
  name,
  defaultValue,
  index,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const isVisible = context.utils.isVisible(field, index);
  const isEditable = context.utils.isEditable(field, index);
  const toggleSwitchClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_toggleswitch',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );

  const isRequired = useRequired(field, context.values);

  const options = field.options
    ? field.options.map((option) => {
        return {
          ...option,
        } as ToggleSwitchOption;
      })
    : [];

  if (!options || options.length === 0) {
    return <></>;
  }

  const defaultValueProps = {
    ...(defaultValue && {
      defaultValue: defaultValue as PathValue<I, Path<I>>,
    }),
  };

  return (
    <FormField
      labelText={field.label}
      tooltip={field.tooltip}
      fieldName={name}
      required={isRequired}
      helper={field.description}
      className={toggleSwitchClassNames}
      data-testid={`FormGenerator__ToggleSwitchField_Container_${field.name}`}
      control={(formMethods, props) => (
        <Controller
          control={formMethods.control as Control<Record<string, unknown>>}
          {...defaultValueProps}
          name={name}
          rules={{
            required: isRequired ? `${field.label} is required.` : undefined,
          }}
          render={({ field: { onChange, value } }) => (
            <ToggleSwitch
              initialValue={value}
              data-testid={`FormGenerator__ToggleSwitchField_${field.name}`}
              handleSwitchChange={onChange}
              className={props.className}
            >
              {options?.map(
                ({ value: optionValue, label, readonly, ...otherProps }) => (
                  <Button
                    key={optionValue as string | number}
                    type="button"
                    data-testid={`FormGenerator__ToggleSwitchField_${field.name}_Option_${optionValue}`}
                    disabled={!isEditable || readonly || field.readonly}
                    value={optionValue}
                    sizeType="smaller"
                    {...otherProps}
                  >
                    {label}
                  </Button>
                ),
              )}
            </ToggleSwitch>
          )}
        />
      )}
      fixedHeightFeedback={context.options.display.feedback.reserveSpace}
      customGetErrorMessage={field.customGetErrorMessage}
      customIsErrorValidation={field.customIsErrorValidation}
    />
  );
};

export default ToggleSwitchField;
