import { fieldRequired } from '../utils';

// Types
import { Field, BaseFieldType } from '../types';
import { DeepPartial } from 'react-hook-form';

export const useRequired = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  values: DeepPartial<I>,
): boolean => {
  return fieldRequired(field, values);
};

export default useRequired;
