import { useCallback, useContext, useMemo } from 'react';

// Components
import Select from '../../Select';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Types
import { ActionProps, BaseFieldType } from '../types';

const Bulk = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
}: ActionProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);

  const onChange = useCallback(
    (value) => {
      value.value &&
        context.utils.configureTable(
          field,
          {
            mode: value.value,
          },
          true,
        );
    },
    [field, context.utils],
  );

  const bulkOptions = useMemo(() => {
    return [
      {
        label: context.options.labels?.['bulk.single'] || 'Single',
        value: 'single',
      },
      {
        label: context.options.labels?.['bulk.bulk'] || 'Bulk',
        value: 'bulk',
      },
    ];
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [context.hash]);
  return (
    <Select
      id={`${context.formId}_${field.name}_bulk`}
      options={bulkOptions}
      onChange={onChange}
      defaultValue={{
        label: context.options.labels?.['bulk.single'] || 'Single',
        value: 'single',
      }}
    />
  );
};

export default Bulk;
