import { useContext, useMemo } from 'react';
import classNames from 'classnames';

// Components
import FormCheckboxField from '../../FormCheckboxField';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import useRequired from '../Hooks/useRequired';
import useFieldReadOnly from '../Hooks/useFieldReadOnly';
import useFieldDisabled from '../Hooks/useFieldDisabled';

// Utils
import { isStringArray } from './fieldTypes.utils';

// Types
import { BaseFieldType, FieldProps } from '../types';
import { FormCheckboxFieldOption } from 'types';

// Styles
import './Field.scss';

const CheckboxField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  defaultValue,
  index,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const isVisible = context.utils.isVisible(field, index);
  const isDisabled = useFieldDisabled(field, index);
  const isReadOnly = useFieldReadOnly(field, index);
  const checkboxClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_checkbox',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );

  const isRequired = useRequired(field, context.values);

  const options = field.options
    ? (field.options as FormCheckboxFieldOption[]).map((option) => {
        return {
          ...option,
          readonly: isDisabled || option?.readonly,
        };
      })
    : [];

  if (!options || options.length === 0) {
    return <></>;
  }

  if (
    !(
      typeof defaultValue === 'string' ||
      typeof defaultValue === 'number' ||
      isStringArray(defaultValue) ||
      defaultValue === undefined
    )
  ) {
    throw new Error('Unsupported value type provided to CheckboxField');
  }

  // Passing undefined to defaultValue appears to clear certain fields. Using this method, we avoid passing the prop at all if it's not needed
  const defaultValueProps = {
    ...(defaultValue && { defaultValue: defaultValue }),
  };

  return (
    <FormCheckboxField
      labelText={field.label}
      tooltip={field.tooltip}
      fieldName={name}
      required={isRequired && !isReadOnly}
      helper={field.description}
      className={checkboxClassNames}
      testMetadata={{
        wrapper: {
          'data-testid': `FormGenerator__CheckboxField_Container_${field.name}`,
        },
        input: {
          'data-testid': `FormGenerator__CheckboxField_${field.name}`,
        },
      }}
      options={options}
      horizontal={field.layout === 'horizontal'}
      inputProps={{
        ...defaultValueProps,
      }}
      fixedHeightFeedback={context.options.display.feedback.reserveSpace}
      customGetErrorMessage={field.customGetErrorMessage}
      customIsErrorValidation={field.customIsErrorValidation}
      readOnly={isReadOnly}
    />
  );
};

export default CheckboxField;
