import { useContext } from 'react';

// Components
import { GridArea } from '../Grid';

// Context
import { FormGeneratorContext } from './FormGenerator';

// Types
import { BaseFieldType, Field, FieldMap } from './types';

export interface FieldsProps<
  I extends Record<string, unknown>,
  N = BaseFieldType,
> {
  fields?: Field<I, N>[];
}

const Fields = <I extends Record<string, unknown>, N = BaseFieldType>({
  fields,
}: FieldsProps<I, N>): JSX.Element | null => {
  const context = useContext(FormGeneratorContext);

  if (!fields) {
    return null;
  }

  return (
    <>
      {fields.map((field: Field<I, N>) => {
        const FieldType = context.options.fieldTypes[field.type];
        const FieldDetails = context.fieldMap.find(
          (map: FieldMap<I>) => map.name === field.name,
        );
        if (!FieldType || !FieldDetails.visible) {
          return;
        }

        if (field.type !== 'hidden') {
          return (
            <GridArea
              key={`${context.formId}_${field.name}`}
              {...context.utils.calculateSizes(field)}
              className="sb-form-generator_field-grid"
            >
              <FieldType field={field} name={field.name}>
                {field?.fields && field.type !== 'tablerepeater' ? (
                  <Fields<I, N> fields={field.fields} />
                ) : (
                  ''
                )}
              </FieldType>
            </GridArea>
          );
        } else {
          return (
            <FieldType
              key={`${context.formId}_${field.name}`}
              field={field}
              name={field.name}
            />
          );
        }
      })}
    </>
  );
};

export default Fields;
