import { useCallback, useContext, useMemo, useState } from 'react';
import classNames from 'classnames';

// Components
import { Grid } from '../../Grid';
import Typography from '../../Typography';
import TooltipTemplate from '../../TooltipTemplate';
import Clear from '../../FormGenerator/Actions/Clear';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Styles
import './Section.scss';

// Types
import { BaseFieldType, FieldProps } from '../types';
import CollapsiblePanel from '../../CollapsiblePanel';

const Section = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  children,
  index,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const styles = context.options.styles?.section || {};
  const isVisible = context.utils.isVisible(field, index);
  const [collapsed, setCollapsed] = useState(false);
  const clearButtonAction = field?.actions?.find(
    (action) => action.type === 'clear',
  );

  const sectionClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_section',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );

  const handleCollapse = useCallback(() => {
    setCollapsed(!collapsed);
  }, [collapsed]);

  const SectionHeader = (): JSX.Element => (
    <div
      className={classNames('sb-form-generator_section-header', {
        'sb-form-generator_section-header--collapsible': field.collapsible,
      })}
      data-testid="FormGenerator__SectionField_Header"
    >
      <div>
        <Typography
          variant="T40"
          className="sb-form-generator_label sb-form-generator_section-label"
        >
          {field.label}
          {field.tooltip && (
            <TooltipTemplate
              tooltipContent={field.tooltip}
              size="small"
              testMetadata={{
                icon: {
                  'data-testid': `FormGenerator__SectionField_Tooltip_${field.name}`,
                },
              }}
            />
          )}
        </Typography>
        {field.description && (
          <Typography
            variant="T15"
            className="sb-form-generator_description sb-form-generator_section-description"
          >
            {field.description}
          </Typography>
        )}
      </div>
      {clearButtonAction && <Clear fields={field.fields} />}
    </div>
  );

  return (
    <div
      className={sectionClassNames}
      data-testid={`FormGenerator__SectionField_${field.name}`}
      style={styles}
    >
      {field.collapsible ? (
        <CollapsiblePanel
          borderless
          toggleIcon="chevron-down"
          isOpen={collapsed}
          onToggleOpen={handleCollapse}
          toggleBarClick={clearButtonAction ? 'icon' : 'full'}
          renderToggleBar={SectionHeader}
          className="sb-form-generator_section--collapsible"
          testMetadata={{
            collapsiblePanel: {
              'data-testid': `FormGenerator__SectionField_${field.name}-collapsible`,
            },
            content: {
              'data-testid': `FormGenerator__SectionField_${field.name}-collapsibleContent`,
            },
          }}
          toggleClass="sb-form-generator_section--collapsible-icon"
        >
          <Grid density="simple">{children}</Grid>
        </CollapsiblePanel>
      ) : (
        <>
          {field.label && <SectionHeader />}
          <Grid density="simple">{children}</Grid>
        </>
      )}
    </div>
  );
};

export default Section;
