import { AnySchema } from 'yup';

// Utilities
import {
  fieldEditable,
  fieldVisible,
  isDependantFieldValueEqual,
  isDependantFieldValueNull,
} from '../utils';

// Types
import {
  BaseFieldType,
  CheckboxFieldOption,
  Field,
  FieldValidator,
  FormResolverContext,
} from '../types';

const required = <I extends Record<string, unknown>, N = BaseFieldType>(
  field: Field<I, N>,
  yup: AnySchema,
  validator?: FieldValidator,
): AnySchema => {
  return yup.test(
    'required',
    validator?.message ?? `${String(field.label || field.name)} is required`,
    // Must be a declared function (not an arrow function) if you need access to the `this` context from yup
    function (value) {
      const dependentFieldValue = validator?.meta?.dependentFieldValue;
      const dependentFieldNull = validator?.meta?.dependentFieldNull;
      const dependentField = validator?.meta?.dependentField;
      const context = this?.options?.context as FormResolverContext<I, N>;

      if (context) {
        const isVisible = fieldVisible(
          field,
          context.options,
          this.parent,
          context.breakpoint,
        );

        const isEditable = fieldEditable(
          field,
          context.getFormValuesRef?.current(),
        );

        if (!isVisible || field?.readonly || !isEditable) {
          return true;
        }

        if (
          context.savingDraftRef?.current &&
          !context.options?.drafts?.validate
        )
          return true;
      }

      if (field.value === null || value === null) {
        return false;
      }

      if (field.type === 'checkbox') {
        const oneChecked = field.options?.some((option) => {
          const checkboxOption = option as CheckboxFieldOption;
          return this.parent[checkboxOption.id] === true;
        });
        if (!oneChecked) {
          return false;
        }
      } else if (field.type === 'daterange') {
        if (!value?.startDate || !value?.endDate) {
          return false;
        }
      } else if (
        (dependentField &&
          ((dependentFieldValue &&
            isDependantFieldValueEqual(
              this.parent[dependentField],
              dependentFieldValue,
            )) ||
            (dependentFieldNull &&
              isDependantFieldValueNull(this.parent[dependentField])))) ||
        !dependentField
      ) {
        if (
          typeof value === 'undefined' ||
          value === null ||
          (Array.isArray(value) && value.length === 0) ||
          (typeof value === 'string' && value.trim().length === 0) ||
          (typeof value === 'object' &&
            !(value instanceof Date) &&
            Object.keys(value).length === 0)
        ) {
          return false;
        }
        return true;
      }
      return true;
    },
  );
};

export default required;
