import { useMemo } from 'react';
import { DeepPartial, Path } from 'react-hook-form';
import * as Yup from 'yup';

// Types
import {
  BaseFieldType,
  CheckboxFieldOption,
  Field,
  FieldMap,
  FormOptions,
  FormSchema,
  ParseSchemaState,
  SchemaState,
  ValidatorOptions,
} from '../types';

// Utils
import { generateField, getFieldDefault } from '../utils';

export const createField = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  validators: ValidatorOptions<I, N>,
): Yup.AnySchema => {
  let Field = Yup.mixed();

  if (Array.isArray(field.validator)) {
    field.validator.forEach((fieldValidator) => {
      const validatorKey =
        typeof fieldValidator === 'string'
          ? fieldValidator
          : fieldValidator.type;
      if (validatorKey && validators[validatorKey]) {
        Field = validators[validatorKey](field, Field, {
          type: validatorKey,
          value:
            typeof fieldValidator === 'object'
              ? fieldValidator.value
              : undefined,
          meta:
            typeof fieldValidator === 'object'
              ? fieldValidator?.meta
              : undefined,
          message:
            typeof fieldValidator === 'object'
              ? fieldValidator?.message
              : undefined,
        });
      } else {
        console.error(`Validator not found: ${validatorKey}`);
      }
    });
  }

  return Field;
};

export const parseOptions = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  parentField: Field<I, N> | undefined,
  fieldOptions: ReadonlyArray<CheckboxFieldOption> | undefined,
  options: FormOptions<I, N>,
): ParseSchemaState<I, N> => {
  if (!parentField || !fieldOptions) {
    return {
      fieldRef: {},
      fieldMap: [],
      defaultValues: {},
      validationSchema: {},
    };
  }

  return fieldOptions.reduce(
    (acc: ParseSchemaState<I, N>, option: CheckboxFieldOption) => {
      const fieldVisible = generateField(parentField, options);
      // Create Field map
      const fieldName = (option.name || option.value) as Path<I>;

      // Create Validation Schema
      if (fieldVisible && parentField.validator) {
        acc.validationSchema[fieldName] = createField(
          {
            ...parentField,
            name: fieldName,
          },
          options.validators,
        );
      }

      return acc;
    },
    {
      fieldRef: {},
      fieldMap: [],
      defaultValues: {},
      validationSchema: {},
    } as ParseSchemaState<I, N>,
  );
};

export const parseFields = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  fields: Field<I, N>[] | undefined,
  options: FormOptions<I, N>,
  parseDefaults: boolean,
  parent?: Field<I, N>,
): ParseSchemaState<I, N> => {
  if (!fields) {
    return {
      fieldRef: {},
      fieldMap: [],
      defaultValues: {},
      validationSchema: {},
    };
  }

  return fields.reduce(
    (acc: ParseSchemaState<I, N>, field: Field<I, N>) => {
      const fieldVisible = generateField(field, options);
      // Create Field map
      const fieldData = { ...field, visible: fieldVisible };
      acc.fieldMap.push({
        ...fieldData,
        ...(parent !== undefined && { parent: parent }),
      });
      acc.fieldRef[fieldData.name] = fieldData;

      // Create Default Values
      if (parseDefaults) {
        if (field.value) {
          acc.defaultValues[field.name] = field.value;
        }
      }

      // Create Validation Schema
      if (fieldVisible && field.validator) {
        acc.validationSchema[field.name] = createField(
          field,
          options.validators,
        );
      }

      if (fieldVisible && field.fields && field.fields.length) {
        const subfields = parseFields(
          field.fields,
          options,
          parseDefaults,
          field,
        );
        acc = {
          fieldMap: acc.fieldMap.concat(subfields.fieldMap),
          fieldRef: {
            ...acc.fieldRef,
            ...subfields.fieldRef,
          },
          defaultValues: {
            ...acc.defaultValues,
            ...subfields.defaultValues,
          },
          validationSchema: {
            ...acc.validationSchema,
            ...(field.type === 'tablerepeater'
              ? {
                  [field.name]: Yup.array().of(
                    Yup.object(subfields.validationSchema),
                  ),
                }
              : subfields.validationSchema),
          },
        };
      }

      if (fieldVisible && field.type === 'checkbox' && field.options?.length) {
        const optionsFields = parseOptions(
          field,
          field.options as ReadonlyArray<CheckboxFieldOption>,
          options,
        );

        acc = {
          fieldRef: acc.fieldRef,
          fieldMap: acc.fieldMap,
          defaultValues: acc.defaultValues,
          validationSchema: {
            ...acc.validationSchema,
            ...optionsFields.validationSchema,
          },
        };
      }

      return acc;
    },
    {
      fieldRef: {},
      fieldMap: [],
      defaultValues: {},
      validationSchema: {},
    } as ParseSchemaState<I, N>,
  );
};

export const useSchemaState = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  form: FormSchema<I, N>,
  options: FormOptions<I, N>,
): SchemaState<I, N> => {
  const formHash = JSON.stringify(form);

  const { fieldRef, fieldMap, defaultValues, validationSchema } = useMemo(
    () => parseFields(form.fields, options, Boolean(!options.defaultValues)),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [formHash],
  );

  const initialValues: DeepPartial<I> = useMemo(
    () =>
      fieldMap.reduce((acc, field: FieldMap<I, N>) => {
        if (
          !field.visible ||
          field.type === 'section' ||
          field.detached ||
          field?.parent?.type === 'tablerepeater'
        ) {
          return acc;
        }
        if (field.type === 'checkbox') {
          const defaults = field.options
            ? (field.options as ReadonlyArray<CheckboxFieldOption>).reduce(
                (acc, option) => {
                  return {
                    ...acc,
                    [option.id]: getFieldDefault(
                      field,
                      options.fieldTypeDefaultValues,
                    ),
                  };
                },
                {},
              )
            : {};
          return {
            ...acc,
            ...defaults,
          };
        }
        if (field.type === 'tablerepeater') {
          const defaultRepeater = getFieldDefault(
            field,
            options.fieldTypeDefaultValues,
          );
          if (Array.isArray(defaultRepeater) && defaultRepeater.length > 0) {
            return {
              ...acc,
              [field.name]: getFieldDefault(
                field,
                options.fieldTypeDefaultValues,
              ),
            };
          }
          return acc;
        }
        return {
          ...acc,
          [field.name]: getFieldDefault(field, options.fieldTypeDefaultValues),
        };
      }, {} as DeepPartial<I>),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [formHash],
  );

  return {
    fieldRef: fieldRef,
    fieldMap: fieldMap,
    defaultValues: {
      ...initialValues,
      ...(options.defaultValues || (defaultValues as DeepPartial<I>)),
    },
    validationSchema: Yup.object().shape(validationSchema),
  };
};

export default useSchemaState;
