import { useContext, useMemo } from 'react';
import classNames from 'classnames';
import useUpdateFieldValue from '../Hooks/useUpdateFieldValue';
import useWatchFields from '../Hooks/useWatchFields';

// Components
import FormReadOnlyField from '../../FormReadOnlyField';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Styles
import './Field.scss';

// Types
import { BaseFieldType, FieldProps } from '../types';

const ReadOnlyField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  loadName,
  index,
  defaultValue,
  isDeleting = false,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const fieldsWatch = useWatchFields(context.values, field.watch, index);
  const isVisible = context.utils.isVisible(field, index);

  const inputClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_readonly',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );

  useUpdateFieldValue(
    name,
    loadName || field.name,
    fieldsWatch,
    context.values,
    context.transport,
    undefined,
    isDeleting,
  );

  const defaultValueProps = {
    ...(defaultValue && { defaultValue: String(defaultValue) }),
  };

  return (
    <FormReadOnlyField
      truncate={field.truncate}
      fieldName={name}
      labelText={field.label}
      tooltip={field.tooltip}
      helper={field.description}
      className={inputClassNames}
      testMetadata={{
        wrapper: {
          'data-testid': `FormGenerator__ReadOnlyField_Container_${field.name}`,
        },
        input: {
          'data-testid': `FormGenerator__ReadOnlyField_${field.name}`,
        },
      }}
      fixedHeightFeedback={context.options.display.feedback.reserveSpace}
      customGetErrorMessage={field.customGetErrorMessage}
      customIsErrorValidation={field.customIsErrorValidation}
      {...defaultValueProps}
    />
  );
};

export default ReadOnlyField;
