import { ReactNode } from 'react';
import { Control, Controller, Path, RegisterOptions } from 'react-hook-form';
import { OptionTypeBase, Props as ReactSelectProps } from 'react-select';

// Components
import FormField, { FormFieldProps } from '../FormField';
import { MultiSelect } from '../MultiSelect';
import FormReadOnlyFieldListValue from '../FormReadOnlyFieldListValue';

// Types
import { TestMetadata } from '../types';

export interface Option {
  value: string;
  label: string;
  isDisabled?: boolean;
}

export type MultiSelectProps<T extends OptionTypeBase> = ReactSelectProps<
  T,
  true
>;

export interface FormMultiSelectFieldProps<I extends Record<string, unknown>> {
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Options to have in the Select */
  options?: Option[];
  /** Props to spread down to the select */
  selectProps?: MultiSelectProps<Option>;
  /** Mark field required (*) */
  required?: boolean;
  /** Placeholder value */
  placeholder?: ReactNode;
  /** Initial value selected */
  initialValue?: Option[];
  /** Rules to pass down to control */
  rules?: RegisterOptions;
  /** Sets helper */
  helper?: string;
  /** Class name */
  className?: string;
  /** Is retrieving data */
  isFetching?: boolean;
  /** Enables the select all option */
  showSelectAllOption?: boolean;
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    input?: TestMetadata;
  };
  defaultValue?: unknown;
  labelFor?: string;
  /** items can be added to selected options */
  creatable?: boolean;
  /** options can be loaded asynchronously */
  async?: boolean;
}

const FormMultiSelectField = <I extends Record<string, unknown>>({
  fieldName,
  labelText,
  tooltip,
  options,
  selectProps,
  required,
  placeholder,
  initialValue,
  rules,
  testMetadata,
  helper,
  className,
  isFetching,
  defaultValue,
  labelFor,
  showSelectAllOption,
  creatable,
  async,
  readOnly,
  ...otherProps
}: FormMultiSelectFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  return (
    <FormField<I>
      labelFor={labelFor}
      labelText={labelText}
      tooltip={tooltip}
      fieldName={fieldName}
      required={required}
      helper={helper}
      readOnly={readOnly}
      className={className}
      control={(formMethods, props) => (
        <Controller
          name={fieldName as string}
          control={formMethods.control as Control<Record<string, unknown>>}
          defaultValue={defaultValue !== undefined ? defaultValue : null}
          rules={{
            required: required ? `${labelText} is required.` : undefined,
            ...rules,
          }}
          render={({ field: { onChange, onBlur, value, name } }) => {
            if (readOnly) {
              return (
                <FormReadOnlyFieldListValue
                  values={
                    value
                      ? (value as Option[]).map((v: Option) => v.label)
                      : undefined
                  }
                  testMetadata={testMetadata?.input}
                />
              );
            }

            return (
              <MultiSelect
                inputId={fieldName as string}
                options={options}
                defaultValue={initialValue}
                placeholder={placeholder}
                isFetching={isFetching}
                onChange={onChange}
                onBlur={onBlur}
                value={value as Option[]}
                name={name}
                customClear={false}
                showSelectAllOption={showSelectAllOption}
                creatable={creatable}
                async={async}
                {...testMetadata?.input}
                {...selectProps}
                {...props}
              />
            );
          }}
        />
      )}
      {...testMetadata?.wrapper}
      {...otherProps}
    />
  );
};

export default FormMultiSelectField;
