import Action from './Action';
import Field from './Field';

// Types
import { BaseFieldType, Field as FieldType } from '../../types';

const Actions = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
}: {
  field: FieldType<I, N>;
}): JSX.Element => {
  if (!field.actions) {
    return <></>;
  }
  return (
    <div className="sb-form-generator_tablerepeater-header_actions">
      {field.actions.map((action, index) => {
        if (action.field) {
          return <Field key={index} field={action.field} />;
        } else {
          return <Action key={index} action={action} field={field} />;
        }
      })}
    </div>
  );
};

export default Actions;
