import { AnySchema } from 'yup';
import { fieldEditable } from '../utils';

// Types
import {
  BaseFieldType,
  Field,
  FieldValidator,
  FormResolverContext,
} from '../types';

const pattern = <I extends Record<string, unknown>, N = BaseFieldType>(
  field: Field<I, N>,
  yup: AnySchema,
  validator: FieldValidator,
): AnySchema => {
  return yup.test(
    'pattern',
    validator.message ?? `${String(field.label || field.name)} is invalid`,
    function (value) {
      const context = this?.options?.context as FormResolverContext<I, N>;

      if (context) {
        const isEditable = fieldEditable(
          field,
          context.getFormValuesRef?.current(),
        );

        if (field?.readonly || !isEditable) {
          return true;
        }
      }

      if (field.type === 'input' || field.type === 'inputmask') {
        if (validator.value instanceof RegExp) {
          return validator.value.test(value);
        }
        return RegExp(validator.value as string).test(value);
      }
      return true;
    },
  );
};

export default pattern;
