import isBoolean from 'lodash/isBoolean';

// Type
import { FC } from 'react';
import { TestMetadata } from '../types';

// Components
import FormReadOnlyFieldTextValue from '../FormReadOnlyFieldTextValue';

export interface FormReadOnlyFieldBooleanValueProps {
  testMetadata?: TestMetadata;
  value?: boolean;
}

const FormReadOnlyFieldBooleanValue: FC<FormReadOnlyFieldBooleanValueProps> = ({
  value,
  testMetadata,
}) => {
  let formattedValue;

  if (isBoolean(value)) {
    formattedValue = value ? 'Yes' : 'No';
  }

  return (
    <FormReadOnlyFieldTextValue
      testMetadata={testMetadata}
      value={formattedValue}
    />
  );
};

export default FormReadOnlyFieldBooleanValue;
