// Components
import FormField, { FormFieldProps } from '../FormField';
import Typography from '../Typography';

// Types
import { TestMetadata, TypographyVariants } from 'types';
import { Control, Controller, Path } from 'react-hook-form';

export interface FormReadOnlyFieldProps<I extends Record<string, unknown>> {
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Display name for field */
  helper?: string;
  /** Class name */
  className?: string;
  /** Variant option for typography component */
  variant?: TypographyVariants;
  /** Default value to use */
  defaultValue?: string;
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    input?: TestMetadata;
  };
  /** Control text overflow behavior, truncate to add ellipsis and complete text on hover, wrap to wrap text  */
  truncate?: 'truncate' | 'wrap';
}

const FormReadOnlyField = <I extends Record<string, unknown>>({
  fieldName,
  labelText,
  tooltip,
  required,
  helper,
  variant = 'T15',
  testMetadata,
  defaultValue,
  truncate = 'truncate',
  ...otherProps
}: FormReadOnlyFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  return (
    <FormField<I>
      labelText={labelText}
      fieldName={fieldName}
      tooltip={tooltip}
      required={required}
      helper={helper}
      {...testMetadata?.wrapper}
      control={(formMethods, props) => (
        <Controller
          name={fieldName as string}
          control={formMethods.control as Control<Record<string, unknown>>}
          defaultValue={defaultValue !== undefined ? defaultValue : null}
          render={({ field: { value } }) => (
            <Typography
              {...props}
              truncate={truncate === 'truncate' && !!value}
              wrap={truncate === 'wrap'}
              variant={variant}
              data-testid={`FormReadOnlyField_${String(fieldName)}`}
              {...testMetadata?.input}
            >
              {value || <span>&nbsp;</span>}
            </Typography>
          )}
        />
      )}
      {...otherProps}
    />
  );
};

export default FormReadOnlyField;
