import {
  Control,
  Controller,
  FieldValues,
  Path,
  RegisterOptions,
} from 'react-hook-form';
import { ReactDatePickerProps } from 'react-datepicker';
import classNames from 'classnames';

// Components
import DateInput from '../DateInput';
import FormField, { FormFieldProps } from '../FormField';
import FormReadOnlyFieldDateValue from '../FormReadOnlyFieldDateValue';

// Styles
import './FormDateField.scss';

// Types
import { TestMetadata } from 'types';
import { InputProps } from 'Input';

export interface FormDateFieldProps<I extends Record<string, unknown>> {
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Mark field required (*) */
  required?: boolean;
  /** Disable field (greyed out) */
  disabled?: boolean;
  /** Prevent navigating date selection with keyboard */
  disabledKeyboardNavigation?: boolean;
  /** Format of the date */
  dateFormat?: string;
  /** Sets helper */
  helper?: string;
  /** Class name */
  className?: string;
  /** Props to spread down to the select */
  datePickerProps?: Partial<ReactDatePickerProps>;
  inputProps?: Partial<InputProps>;
  defaultValue?: unknown;
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    input?: TestMetadata;
  };
  /** Rules passed down to control */
  rules?: RegisterOptions;
  /* Label for override */
  labelFor?: string;
}

const FormDateField = <I extends Record<string, unknown>>({
  fieldName,
  labelText,
  tooltip,
  required,
  disabled,
  disabledKeyboardNavigation,
  dateFormat,
  testMetadata,
  rules,
  helper,
  className,
  datePickerProps,
  inputProps,
  defaultValue,
  labelFor,
  readOnly,
  ...otherProps
}: FormDateFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  const formDateFieldClassNames = classNames('sb-date-field', className);
  return (
    <FormField<I>
      labelFor={labelFor}
      labelText={labelText}
      tooltip={tooltip}
      fieldName={fieldName}
      required={required}
      helper={helper}
      className={formDateFieldClassNames}
      readOnly={readOnly}
      {...testMetadata?.wrapper}
      control={(formMethods, props) => (
        <Controller
          /** Appears to be a bug in RHF on Controller for the control types, where it doesn't inherit the generic interface I */
          control={formMethods.control as Control<FieldValues>}
          defaultValue={defaultValue !== undefined ? defaultValue : null}
          rules={{
            required: required ? `${labelText} is required.` : undefined,
            ...rules,
          }}
          render={({ field: { onChange, value } }) => {
            if (readOnly) {
              return (
                <FormReadOnlyFieldDateValue
                  value={value}
                  dateFormat={dateFormat}
                  testMetadata={testMetadata?.input}
                />
              );
            }

            return (
              <DateInput
                todayButton="Today"
                selected={
                  value
                    ? typeof value === 'string'
                      ? new Date(Date.parse(value))
                      : value
                    : null
                }
                onDateChange={onChange}
                disabledKeyboardNavigation={disabledKeyboardNavigation}
                disabled={disabled}
                dateFormat={dateFormat}
                placeholderText={dateFormat}
                inputProps={inputProps}
                testMetadata={{ input: testMetadata?.input }}
                {...datePickerProps}
                {...props}
                className=""
                containerClassName={props.className}
              />
            );
          }}
          name={fieldName as string}
        />
      )}
      {...otherProps}
    />
  );
};

export default FormDateField;
