import { useContext } from 'react';
import { FormGeneratorContext } from '../FormGenerator';

// Types
import { Field, BaseFieldType } from '../types';

export const useFieldReadOnly = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  index?: number,
): boolean => {
  const context = useContext(FormGeneratorContext);
  const isEditable = context.utils.isEditable(field, index);

  return field.readonly || !isEditable;
};

export default useFieldReadOnly;
