import {
  ButtonHTMLAttributes,
  Children,
  cloneElement,
  CSSProperties,
  DetailedHTMLProps,
  isValidElement,
  PropsWithChildren,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import classNames from 'classnames';
import { useOnScreen } from '@dtci/triton/hooks';

// Styles
import './ToggleSwitch.scss';

// Types
import { TestMetadata } from 'types';

export interface ToggleSwitchProps<T> {
  /* Starting location of the switch (by value) */
  initialValue?: T;
  /* Custom class name(s) for component container */
  className?: string;
  /* Function to call on switch change that accepts the new value */
  handleSwitchChange: (value: T) => void;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const ToggleSwitch = <T extends unknown>({
  initialValue,
  className,
  children: childrenProp,
  handleSwitchChange,
  testMetadata = { 'data-testid': 'toggleSwitch' },
  ...otherProps
}: PropsWithChildren<ToggleSwitchProps<T>>): JSX.Element => {
  // Current value of the toggle
  const [value, setValue] = useState(initialValue);
  // Container Ref for the switches
  const switchListRef = useRef<HTMLDivElement>(null);
  // Map values to child indecies
  const valueToIndex = useMemo(() => new Map(), []);
  // Highlight should only render when visible or it won't calculate the right position and size
  const isOnScreen = useOnScreen(switchListRef);

  // Get dimensions of switch component for styling
  const getSwitchDimensions = useCallback(() => {
    const switchsNode = switchListRef.current;

    // Container dimensions

    // Selected switch dimensions
    let currentSwitchMeta;

    if (switchsNode) {
      const children = switchsNode.children;

      if (children.length > 0) {
        const currentSwitch =
          (value !== undefined && children[valueToIndex.get(value)]) ||
          children[0];
        currentSwitchMeta = currentSwitch.getBoundingClientRect();
      }
    }

    return {
      switchsMeta: switchsNode?.getBoundingClientRect(),
      currentSwitch: currentSwitchMeta,
    };
  }, [valueToIndex, value]);

  const getSelectedStyle = (): CSSProperties => {
    const { switchsMeta, currentSwitch } = getSwitchDimensions();
    const startValue =
      currentSwitch && switchsMeta ? currentSwitch.left - switchsMeta.left : 0;

    return {
      left: startValue,
      width: currentSwitch?.width ?? 0,
    };
  };

  const updateSwitchState = useCallback(
    (value) => {
      setValue(value);
      handleSwitchChange(value);
    },
    [handleSwitchChange],
  );

  useEffect(() => {
    setValue(initialValue);
  }, [initialValue]);

  // Setup children with custom toggle switch logic (like event handlers)
  const children = Children.map(childrenProp, (child, index) => {
    if (!isValidElement(child)) {
      console.error(
        'Spellbook: ToggleSwitch - Invalid element provided',
        child,
      );
      return null;
    }

    const childValue =
      child.props.value === undefined ? index : child.props.value;
    valueToIndex.set(childValue, index);
    const selected = (!value && index === 0) || childValue === value;

    return cloneElement(
      child as React.ReactElement<
        DetailedHTMLProps<
          ButtonHTMLAttributes<HTMLInputElement>,
          HTMLInputElement
        >
      >,
      {
        className: classNames(
          child.props.className,
          'sb-toggle-switch-element',
          {
            'sb-toggle-switch-selected': selected,
          },
        ),
        onClick: updateSwitchState.bind(null, childValue),
      },
    );
  });

  const toggleSwitchClasses = useMemo(
    () => classNames('sb-toggle-switch', className),
    [className],
  );

  const selectedStyle = getSelectedStyle();

  return (
    <div {...otherProps} className={toggleSwitchClasses}>
      <div
        className="sb-toggle-switch-elements"
        ref={switchListRef}
        {...testMetadata}
      >
        {children}
      </div>
      {isOnScreen && (
        <span
          className="sb-toggle-switch-highlight"
          style={{
            ...selectedStyle,
          }}
        />
      )}
    </div>
  );
};

export default ToggleSwitch;
