import { useContext } from 'react';

// Components
import Typography from '../../Typography';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Styles
import './FormTitlebar.scss';

const FormTitlebar = (): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  return (
    <div
      className="sb-form-generator_titlebar"
      data-testid="FormGenerator__Component_FormTitlebar"
      {...context.testMetadata?.titlebar}
    >
      {context.options.formName && (
        <Typography variant="T40" className="sb-form-generator_titlebar-title">
          {context.options.formName}
        </Typography>
      )}
      {context.options.formDescription && (
        <Typography
          variant="T20"
          className="sb-form-generator_titlebar-description"
        >
          {context.options.formDescription}
        </Typography>
      )}
    </div>
  );
};

export default FormTitlebar;
