import { useContext } from 'react';
import classNames from 'classnames';
import { toolbarPlacement } from '../utils';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Styles
import './Footer.scss';

const Footer = (): JSX.Element | null => {
  const context = useContext(FormGeneratorContext);
  const styles = context.options.styles?.footer || {};
  const placement = toolbarPlacement(
    context.currentBreakpoint,
    context.options.toolbar?.location,
  );
  const isSticky = context.options.footer?.isSticky;

  if (placement !== 'bottom') {
    return null;
  }

  const Toolbar = context.options.components.toolbar;

  return (
    <div
      className={classNames('sb-form-generator_footer', {
        'sb-form-generator_footer-sticky': isSticky,
      })}
      data-testid="FormGenerator__Component_Footer"
      {...context.testMetadata?.footer}
      style={styles}
    >
      <Toolbar />
    </div>
  );
};

export default Footer;
