import { useContext, useMemo } from 'react';
import classNames from 'classnames';

// Components
import FormDateRangeField, {
  FormDateRangeValue,
} from '../../FormDateRangeField';

// Context
import useFieldReadOnly from '../Hooks/useFieldReadOnly';
import useFieldDisabled from '../Hooks/useFieldDisabled';
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import useRequired from '../Hooks/useRequired';
import useWatchFields from '../Hooks/useWatchFields';
import useUpdateFieldValue from '../Hooks/useUpdateFieldValue';

// Types
import { BaseFieldType, FieldProps } from '../types';

// Styles
import './Field.scss';

const DateRangeField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  loadName,
  name,
  defaultValue,
  index,
  isDeleting = false,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const fieldsWatch = useWatchFields(context.values, field.watch, index);
  const isVisible = context.utils.isVisible(field, index);
  const isDisabled = useFieldDisabled(field, index);
  const isReadOnly = useFieldReadOnly(field, index);
  const dateClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_date',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );

  useUpdateFieldValue(
    name,
    loadName || field.name,
    fieldsWatch,
    context.values,
    context.transport,
    undefined,
    isDeleting,
  );

  const isRequired = useRequired(field, context.values);

  // Passing undefined to defaultValue appears to clear certain fields. Using this method, we avoid passing the prop at all if it's not needed
  const defaultValueProps = {
    ...(defaultValue && { defaultValue: defaultValue as FormDateRangeValue }),
  };

  const id =
    index !== undefined
      ? `${context.formId}_${index}_${field.name}`
      : `${context.formId}_${field.name}`;
  return (
    <FormDateRangeField
      labelFor={id}
      key={name}
      fieldName={name}
      labelText={field.label}
      tooltip={field.tooltip}
      helper={field.description}
      required={isRequired && !isReadOnly}
      className={dateClassNames}
      dateFormat={field.placeholder}
      readOnly={isReadOnly}
      testMetadata={{
        wrapper: {
          'data-testid': `FormGenerator__DateRangeField_Container_${field.name}`,
        },
        dateRangeInput: {
          startDate: {
            input: {
              'data-testid': `FormGenerator__DateRangeField_${field.name}_Start`,
            },
          },
          endDate: {
            input: {
              'data-testid': `FormGenerator__DateRangeField_${field.name}_End`,
            },
          },
        },
      }}
      inputDateProps={{
        disabled: isDisabled,
      }}
      startDateProps={{
        id: `${id}_Start`,
      }}
      endDateProps={{
        id: `${id}_End`,
      }}
      fixedHeightFeedback={context.options.display.feedback.reserveSpace}
      {...defaultValueProps}
    />
  );
};

export default DateRangeField;
