import { ComponentProps, FC } from 'react';

// Components
import Button from '../Button';

// Types
import { TestMetadata } from 'types';

export type AddButtonProps = ComponentProps<typeof Button> & {
  // Test IDs for sections of AddButton
  testMetadata?: TestMetadata;
};

const AddButton: FC<AddButtonProps> = ({
  children = 'Add',
  isFetching,
  iconType = 'fa-plus',
  sizeType = 'small',
  testMetadata,
  ...other
}) => (
  <Button
    className="sb-add-button sb-action-button"
    iconType={iconType}
    disabled={isFetching}
    isFetching={isFetching}
    sizeType={sizeType}
    {...testMetadata}
    {...other}
  >
    {children}
  </Button>
);

export default AddButton;
