import { useContext, useMemo } from 'react';
import classNames from 'classnames';

// Components
import FormMaskField from '../../FormMaskField';
import TextField from './TextField';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import useRequired from '../Hooks/useRequired';
import useFieldReadOnly from '../Hooks/useFieldReadOnly';
import useFieldDisabled from '../Hooks/useFieldDisabled';

// Utils
import { isStringArray } from './fieldTypes.utils';

// Styles
import './Field.scss';

// Types
import { BaseFieldType, FieldProps } from '../types';
import { Path } from 'react-hook-form';

const InputMaskField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  defaultValue,
  index,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const isVisible = context.utils.isVisible(field, index);
  const isDisabled = useFieldDisabled(field, index);
  const isReadOnly = useFieldReadOnly(field, index);
  const inputClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_inputmask',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );

  const isRequired = useRequired(field, context.values);

  if (
    !(
      typeof defaultValue === 'string' ||
      typeof defaultValue === 'number' ||
      isStringArray(defaultValue) ||
      defaultValue === undefined
    )
  ) {
    throw new Error('Unsupported value type provided to InputMaskField');
  }

  const defaultValueProps = {
    ...(defaultValue && { defaultValue: defaultValue }),
  };

  if (!field?.mask && !field?.mask?.input) {
    console.warn(
      'FormGenerator:InvalidInput - InputMask requires a mask to be defined. Rendering standard input.',
    );
    return (
      <TextField
        field={field}
        name={name}
        defaultValue={defaultValue}
        index={index}
      />
    );
  }

  return (
    <FormMaskField
      mask={field.mask.input}
      fieldName={name as Path<I>}
      labelText={field.label}
      tooltip={field.tooltip}
      helper={field.description}
      required={isRequired && !isReadOnly}
      className={inputClassNames}
      testMetadata={{
        wrapper: {
          'data-testid': `FormGenerator__InputMaskField_Container_${field.name}`,
        },
        input: {
          'data-testid': `FormGenerator__InputMaskField_${field.name}`,
        },
      }}
      maskedInputProps={{
        maskCharacter: field.mask.character,
        alwaysShowMask: field.mask.alwaysShow,
        inputProps: {
          width: 'auto',
          disabled: isDisabled,
          className: 'sb-form-generator_inputmask-input',
          placeholder: field.placeholder,
          ...defaultValueProps,
        },
      }}
      fixedHeightFeedback={context.options.display.feedback.reserveSpace}
      customGetErrorMessage={field.customGetErrorMessage}
      customIsErrorValidation={field.customIsErrorValidation}
      readOnly={isReadOnly}
      truncate={field.truncate}
    />
  );
};

export default InputMaskField;
