import classNames from 'classnames';
import { Control, Controller, Path, RegisterOptions } from 'react-hook-form';

// Components
import FormField, { FormFieldProps } from '../FormField';
import MaskedInput, { MaskedInputProps } from '../MaskedInput';
import FormReadOnlyFieldTextValue from '../FormReadOnlyFieldTextValue';

// Types
import { TestMetadata } from 'types';

export interface FormMaskFieldProps<I extends Record<string, unknown>> {
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Mask */
  mask: string;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Display name for field */
  helper?: string;
  /** Class name */
  className?: string;
  /** Mark field required (*) */
  required?: boolean;
  /** Rules to pass down to control */
  rules?: RegisterOptions;
  /* Masked Input Props */
  maskedInputProps?: Partial<MaskedInputProps>;
  leavePlaceholders?: boolean;
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    input?: TestMetadata;
  };
}

const FormMaskField = <I extends Record<string, unknown>>({
  fieldName,
  labelText,
  tooltip,
  required,
  rules,
  maskedInputProps,
  helper,
  testMetadata,
  mask,
  leavePlaceholders = false,
  readOnly,
  truncate,
  ...otherProps
}: FormMaskFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  return (
    <FormField<I>
      labelText={labelText}
      fieldName={fieldName}
      tooltip={tooltip}
      required={required}
      helper={helper}
      readOnly={readOnly}
      {...testMetadata?.wrapper}
      control={(formMethods, props) => (
        <Controller
          name={fieldName as string}
          control={formMethods.control as Control<Record<string, unknown>>}
          defaultValue={
            maskedInputProps?.inputProps?.defaultValue !== undefined
              ? maskedInputProps?.inputProps?.defaultValue
              : null
          }
          rules={{
            required: required ? `${labelText} is required.` : undefined,
            ...rules,
          }}
          render={({ field: { onChange, value } }) => {
            const inputTestMetadata = {
              'data-testid': `FormMaskField_${String(fieldName)}`,
              ...testMetadata?.input,
            };

            if (readOnly) {
              return (
                <FormReadOnlyFieldTextValue
                  value={value as string}
                  testMetadata={inputTestMetadata}
                  truncate={truncate}
                />
              );
            }

            return (
              <MaskedInput
                {...testMetadata?.input}
                {...maskedInputProps}
                inputProps={{
                  ...props,
                  ...maskedInputProps?.inputProps,
                  name: fieldName as string,
                  width: 'auto',
                  className: classNames(
                    maskedInputProps?.inputProps?.className,
                    props.className,
                  ),
                  onChange: (e) => {
                    if (e.target.value) {
                      onChange(
                        !leavePlaceholders
                          ? e.target.value.replace(
                              new RegExp(
                                maskedInputProps?.maskCharacter || '_',
                                'g',
                              ),
                              '',
                            )
                          : e.target.value,
                      );
                    }
                  },
                  value: value as string,
                }}
                testMetadata={inputTestMetadata}
                mask={mask}
              />
            );
          }}
        />
      )}
      {...otherProps}
    />
  );
};

export default FormMaskField;
