import { useCallback, useContext } from 'react';

// Components
import Icon from '../../Icon';
import Tab from '../../Tab';
import Tabs from '../../Tabs';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import { usePageState } from '../Hooks/usePageState';

// Styles
import './Pages.scss';

// Types
import { Page } from '../types';

// Utils
import { pagesVisible } from '../utils';
import classNames from 'classnames';

const Pages = (): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const containerStyles = context.options.styles?.paginationContainer || {};
  const styles = context.options.styles?.pagination || {};
  const { pages, dirtyPages, errorPages } = usePageState();
  const isSticky = context.options.header?.areTabsSticky;

  const onClick = useCallback(
    (event, page) => {
      event.preventDefault();
      context.utils.setCurrentPage(page);
    },
    [context.utils],
  );

  const isVisible = pagesVisible(
    context.currentBreakpoint,
    context.gridMode,
    context.screenSize,
    context.form?.pagination,
  );

  if (!isVisible) {
    return <></>;
  }

  return (
    <div
      className={classNames('sb-form-generator_pagination', {
        'sb-form-generator_pagination-sticky': isSticky,
      })}
      data-testid="FormGenerator__Component_Pages"
      style={containerStyles}
      {...context.testMetadata?.pagination}
    >
      <Tabs className="sb-form-generator_pagination-list" style={styles}>
        {pages.map((page: Page) => {
          const pageDirty = dirtyPages.includes(page.name);
          const pageError = errorPages.includes(page.name);

          return (
            <Tab
              key={page.name}
              onClick={(e) => onClick(e, page.name)}
              active={context.currentPage === page.name}
              className={`sb-form-generator_pagination-list-item ${
                pageDirty ? 'sb-form-generator_pagination-list-dirty' : ''
              }`}
              data-testid={`FormGenerator__Component_Page_${page.name}`}
            >
              {pageDirty && !pageError && (
                <Icon
                  className="sb-form-generator_pagination-dirty-icon"
                  altText={`${page.label} has unsaved changes`}
                  faPackage="fas"
                  iconName="fa-asterisk"
                  iconType="fa"
                  size="smaller"
                  title={`${page.label} has unsaved changes`}
                />
              )}
              {pageError && (
                <Icon
                  className="sb-form-generator_pagination-error-icon"
                  altText={`${page.label} has errors`}
                  faPackage="fas"
                  iconName="fa-exclamation-circle"
                  iconType="fa"
                  size="smaller"
                  title={`${page.label} has errors`}
                />
              )}
              <span className="sb-form-generator_pagination-label">
                {page.label}
              </span>
            </Tab>
          );
        })}
      </Tabs>
    </div>
  );
};

export default Pages;
