import { useContext, useEffect } from 'react';
import { FieldValues, Path, useFormContext } from 'react-hook-form';
import isEqual from 'lodash/isEqual';
import get from 'lodash/get';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Types
import { AsyncTransportFunc, MultiFieldValues, SetValueFunc } from '../types';
import { PathValue } from 'react-hook-form';

export const useUpdateFieldValue = <I extends Record<string, unknown>>(
  fieldName: keyof I,
  loadName: string,
  fieldsWatch: unknown | FieldValues | null,
  formValues: I,
  transport?: Partial<Record<keyof I, AsyncTransportFunc<I>>>,
  /** Multifield field type index prop,
   * only used for multifield field type */
  index?: number,
  isDeleting = false,
): void => {
  const { setValue } = useFormContext<I>();

  const { revalidationMode } = useContext(FormGeneratorContext);

  useEffect(() => {
    const updateValue = get(
      transport,
      `${String(loadName)}.updateValue`,
    ) as SetValueFunc<I>;
    if (updateValue && !isDeleting) {
      const currentValue = get(formValues, fieldName) as PathValue<I, Path<I>>;
      const nextValue = updateValue(currentValue, fieldsWatch, formValues);

      let nextFieldValue;
      if (index !== undefined) {
        nextFieldValue = (nextValue as MultiFieldValues[])?.[index];
      } else {
        nextFieldValue = nextValue;
      }
      if (!isEqual(currentValue, nextFieldValue)) {
        setValue(
          fieldName as Path<I>,
          nextFieldValue as PathValue<I, Path<I>>,
          {
            shouldValidate:
              revalidationMode === 'onChange' || revalidationMode === 'onBlur',
          },
        );
      }
    }
    // NOTE: Ignoring `context.values` since update should only occur when the watched field is updated
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [transport, fieldName, fieldsWatch, setValue]);
};

export default useUpdateFieldValue;
