import { ElementType, SyntheticEvent, useContext } from 'react';
import { getFieldDefault } from '../../utils';

// Components
import { TableRow, TableCell } from '../../../Table';
import Typography from '../../../Typography';
import HiddenField from '../HiddenField';
import BulkField from './BulkField';

// Context
import { FormGeneratorContext } from '../../FormGenerator';

// Types
import { BaseFieldType, Fields as FieldsType } from '../../types';
import { FormGeneratorInternalFields } from '../../enum';

// Styles
import './FieldRow.scss';

export interface FieldRowProps<
  I extends Record<string, unknown>,
  N = BaseFieldType,
> {
  fields: FieldsType<I, N>;
  name: string;
  onDelete: (e: SyntheticEvent, i: number) => void;
  itemIndex: number;
  item?: any;
  clearable?: boolean;
  bulk?: number;
  readOnly?: boolean;
  isDeleting?: boolean;
}

const FieldRow = <I extends Record<string, unknown>, N = BaseFieldType>({
  name,
  fields,
  clearable,
  onDelete,
  itemIndex,
  bulk,
  item,
  readOnly,
  isDeleting,
}: FieldRowProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);

  const DeleteButton = context.options.components.repeaterDelete as ElementType;

  const rowRequired = Boolean(item?.[FormGeneratorInternalFields.ROW_REQUIRED]);
  return (
    <TableRow
      data-testid={`FormGenerator_${name}_row`}
      className="sb-form-generator_tablerepeater-table_row"
    >
      {!readOnly && bulk && (
        <TableCell className="sb-form-generator_tablerepeater-table_row_cell sb-form-generator_tablerepeater-table_row_bulk sb-form-generator_tablerepeater-hidden">
          <BulkField name={name} />
        </TableCell>
      )}
      {rowRequired && (
        <TableCell className="sb-form-generator_tablerepeater-table_row_cell sb-form-generator_tablerepeater-table_row_required sb-form-generator_tablerepeater-hidden">
          <HiddenField
            field={{
              name: `${FormGeneratorInternalFields.ROW_REQUIRED}`,
              type: 'hidden',
            }}
            name={`${name}.${itemIndex}.${`${FormGeneratorInternalFields.ROW_REQUIRED}`}`}
            index={itemIndex}
            defaultValue="true"
          />
        </TableCell>
      )}
      {fields.map((field, index) => {
        const FieldType = context.options.fieldTypes[field.type];
        const isHidden = field.type === 'hidden';
        const isDisplay = field.type === 'display';
        const defaultValue =
          item && item[field.name] !== undefined
            ? item[field.name]
            : getFieldDefault(field, context.options.fieldTypeDefaultValues);
        const columnSize = field?.columnSize;
        const styleColumn = {
          ...(!!columnSize && {
            width: columnSize,
          }),
        };
        const rowLocked = Boolean(
          item?.[
            `${FormGeneratorInternalFields.ROW_FIELD_LOCKED}${field.name}`
          ],
        );
        const disabledField = {
          ...(rowRequired &&
            rowLocked && {
              disabled: true,
            }),
        };
        return (
          <TableCell
            key={index}
            className={`sb-form-generator_tablerepeater-table_row_cell sb-form-generator_tablerepeater-table_row_${
              field.name
            } ${isHidden ? 'sb-form-generator_tablerepeater-hidden' : ''}`}
            style={styleColumn}
          >
            {FieldType && bulk && isDisplay ? (
              <Typography variant="T15" className="sb-form-generator_key">
                {bulk} items
              </Typography>
            ) : (
              <>
                <FieldType
                  field={{
                    ...field,
                    ...disabledField,
                    label: undefined, // We are using label in the table header instead
                    readonly: readOnly,
                  }}
                  name={`${name}[${itemIndex}].${field.name}`}
                  index={itemIndex}
                  loadName={`${name}.${field.name}`}
                  parentName={name}
                  defaultValue={defaultValue}
                  isDeleting={isDeleting}
                />
                {rowLocked && (
                  <HiddenField
                    field={{
                      name: `${FormGeneratorInternalFields.ROW_FIELD_LOCKED}${field.name}`,
                      type: 'hidden',
                    }}
                    name={`${name}.${itemIndex}.${`${FormGeneratorInternalFields.ROW_FIELD_LOCKED}${field.name}`}`}
                    index={itemIndex}
                    defaultValue="true"
                  />
                )}
              </>
            )}
          </TableCell>
        );
      })}
      {!readOnly &&
        clearable &&
        !bulk &&
        !item?.[FormGeneratorInternalFields.ROW_REQUIRED] && (
          <TableCell className="sb-form-generator_tablerepeater-table_row_cell sb-form-generator_tablerepeater-table_cell_actions">
            <DeleteButton
              className="sb-form-generator_tablerepeater-delete"
              data-testid="FormGenerator__TableRepeaterField_Delete"
              onClick={(e: SyntheticEvent) => onDelete(e, itemIndex)}
            />
          </TableCell>
        )}
      {!readOnly && item?.[FormGeneratorInternalFields.ROW_REQUIRED] && (
        <TableCell className="sb-form-generator_tablerepeater-table_row_cell sb-form-generator_tablerepeater-table_cell_actions">
          <div className="sb-form-generator_tablerepeater-required sb-form-generator_field sb-form-generator_tablerepeater-table_row_required">
            *Required
          </div>
        </TableCell>
      )}
    </TableRow>
  );
};

export default FieldRow;
