import { AnySchema } from 'yup';

// Utils
import { fieldEditable } from '../utils';

// Types
import {
  BaseFieldType,
  Field,
  FieldValidator,
  FormResolverContext,
} from '../types';

const maxlength = <I extends Record<string, unknown>, N = BaseFieldType>(
  field: Field<I, N>,
  yup: AnySchema,
  validator: FieldValidator,
): AnySchema => {
  return yup.test(
    'max',
    validator.message ??
      `${String(field.label || field.name)} cannot be more than ${
        validator.value
      } characters`,
    function (value) {
      const context = this?.options?.context as FormResolverContext<I, N>;

      if (context) {
        const isEditable = fieldEditable(
          field,
          context.getFormValuesRef?.current(),
        );

        if (field?.readonly || !isEditable) {
          return true;
        }
      }

      if (typeof value === 'number' && value > Number(validator.value)) {
        return false;
      }
      if (typeof value === 'string' && value.length > Number(validator.value)) {
        return false;
      }
      return true;
    },
  );
};

export default maxlength;
