import { useContext } from 'react';
import { FormGeneratorContext } from '../FormGenerator';

// Types
import { Field, BaseFieldType } from '../types';

export const useFieldDisabled = <
  I extends Record<string, unknown>,
  N = BaseFieldType,
>(
  field: Field<I, N>,
  index?: number,
): boolean => {
  const context = useContext(FormGeneratorContext);
  const isEnabled = context.utils.isEnabled(field, index);

  return field.disabled || !isEnabled;
};

export default useFieldDisabled;
