import { useCallback, useContext } from 'react';

// Components
import Button from '../../Button';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Types
import { BaseFieldType, Field } from 'FormGenerator/types';

export interface FieldsProps<
  I extends Record<string, unknown>,
  N = BaseFieldType,
> {
  fields?: Field<I, N>[];
}

const Clear = <I extends Record<string, unknown>, N = BaseFieldType>({
  fields,
}: FieldsProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const defaultValues = context.defaultValues;

  const onClear = useCallback(() => {
    fields?.map((field: Field<I, N>) => {
      context.control.setValue(
        [field.name] as unknown as string,
        (defaultValues && defaultValues[field.name]) || '',
      );
    });
  }, [context.control, defaultValues, fields]);

  return (
    <Button
      styleType="link"
      sizeType="small"
      type="button"
      data-testid="FormGenerator__Clear_Button"
      disabled={context.actionRunning}
      onClick={onClear}
    >
      Clear
    </Button>
  );
};

export default Clear;
