import {
  ElementType,
  SyntheticEvent,
  useContext,
  useMemo,
  useState,
} from 'react';
import classNames from 'classnames';

// Utils
import { fieldRequired } from '../../utils';

// Components
import { FormGeneratorContext } from '../../FormGenerator';
import { TableHead, TableRow, TableHeaderCell } from '../../../Table';
import Input from '../../../Input';

// Types
import {
  Fields as FieldsType,
  Field as FieldType,
  BaseFieldType,
} from '../../types';

// Styles
import './TableHeader.scss';

export interface HeaderProps<
  I extends Record<string, unknown>,
  N = BaseFieldType,
> {
  field: FieldType<I, N>;
  fields: FieldsType<I, N>;
  onAdd: (e: SyntheticEvent, numberToAdd: number) => void;
  readOnly?: boolean;
  disabled?: boolean;
}

const TableHeader = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  fields,
  onAdd,
  readOnly,
  disabled,
}: HeaderProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const [bulkAddInput, setBulkAddInput] = useState<string>('1');

  const AddButton = context.options.components.repeaterAdd as ElementType;

  const actionsClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_tablerepeater-table_header-row_cell',
        'sb-form-generator_tablerepeater-table_header_actions',
        {
          'sb-form-generator_tablerepeater-table_header_actions_bulk':
            field.creatable && field.bulkAdd,
        },
      ),
    [field.creatable, field.bulkAdd],
  );

  return (
    <TableHead className="sb-form-generator_tablerepeater-table_header">
      <TableRow
        className="sb-form-generator_tablerepeater-table_header-row"
        data-testid="FormGenerator__TableRepeater_Table_Header_Row"
      >
        {fields.map((childField, index) => {
          const isHidden = childField.type === 'hidden' ? true : false;
          const isRequired = fieldRequired(childField, context.values);
          const columnSize = childField?.columnSize;
          const styleColumn = {
            ...(!!columnSize && {
              width: columnSize,
            }),
          };

          return (
            <TableHeaderCell
              key={index}
              tableHeaderClassName={`sb-form-generator_tablerepeater-table_header-row_cell sb-form-generator_tablerepeater-table_header-row_${
                childField.name
              } ${isHidden ? 'sb-form-generator_tablerepeater-hidden' : ''}`}
              data-testid="FormGenerator__TableRepeater_Table_Header_Cell"
              style={styleColumn}
            >
              {childField.label}
              {isRequired && !readOnly && (
                <span className="sb-form-generator_tablerepeater-field-required">
                  *
                </span>
              )}
            </TableHeaderCell>
          );
        })}
        {(field.clearable || field.creatable) && !readOnly && (
          <TableHeaderCell
            tableHeaderClassName={actionsClassNames}
            data-testid="FormGenerator__TableRepeater_Table_Header_Actions"
          >
            {field.creatable && (
              <>
                {field.bulkAdd && (
                  <Input
                    type="number"
                    defaultValue={1}
                    min={1}
                    max={99}
                    className="sb-form-generator_tablerepeater-bulk-add-input"
                    data-testid="FormGenerator__TableRepeaterField_Bulk_Add_Input"
                    value={bulkAddInput}
                    onChange={(e) => setBulkAddInput(e.target.value)}
                    disabled={context?.formLocked || disabled}
                  />
                )}
                <AddButton
                  className="sb-form-generator_tablerepeater-add"
                  data-testid="FormGenerator__TableRepeaterField_Add"
                  onClick={(e: SyntheticEvent) =>
                    onAdd(e, Number(bulkAddInput))
                  }
                  disabled={context?.formLocked || disabled}
                />
              </>
            )}
          </TableHeaderCell>
        )}
      </TableRow>
    </TableHead>
  );
};

export default TableHeader;
