import { useContext, useMemo, useState } from 'react';
import classNames from 'classnames';
import get from 'lodash/get';

// Components
import Typography from '../../Typography';
import FormField from '../../FormField';
import { FileUploader } from '../../FileUploader';
import { Controller, Path, PathValue } from 'react-hook-form';
import TooltipTemplate from '../../TooltipTemplate';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Hooks
import useRequired from '../Hooks/useRequired';
import useFieldReadOnly from '../Hooks/useFieldReadOnly';
import useFieldDisabled from '../Hooks/useFieldDisabled';

// Types
import { BaseFieldType, FieldProps } from '../types';

// Styles
import './FilesField.scss';
import { SBFile } from '../../types';

const FilesField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  defaultValue,
  index,
  loadName,
}: FieldProps<I, N>): JSX.Element => {
  const [uploading, setUploading] = useState(false);
  const context = useContext(FormGeneratorContext);
  const isVisible = context.utils.isVisible(field, index);
  const isDisabled = useFieldDisabled(field, index);
  const isReadOnly = useFieldReadOnly(field, index);
  const inputClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_files',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );

  const isRequired = useRequired(field, context.values);

  const defaultValueProps = {
    ...(defaultValue && {
      defaultValue: defaultValue as PathValue<I, Path<I>>,
    }),
  };

  const id =
    index !== undefined
      ? `${context.formId}_${index}_${field.name}`
      : `${context.formId}_${field.name}`;

  return (
    <div
      className={inputClassNames}
      data-testid={`FormGenerator__FilesField_Container_${field.name}`}
    >
      {field.label && (
        <div className="sb-form-generator_files-header">
          <Typography
            variant="T30"
            className="sb-form-generator_label sb-form-generator_files-label"
          >
            {field.label}
            {field.tooltip && (
              <TooltipTemplate
                tooltipContent={field.tooltip}
                size="small"
                testMetadata={{
                  icon: {
                    'data-testid': `FormGenerator__FilesField_Tooltip_${field.name}`,
                  },
                }}
              />
            )}
            {isRequired && !isReadOnly && (
              <span className="sb-form-generator_files-required">
                *Required
              </span>
            )}
            {field.description && !isReadOnly && (
              <Typography
                variant="T15"
                className="sb-form-generator_description sb-form-generator_files-description"
              >
                {field.description}
              </Typography>
            )}
          </Typography>
        </div>
      )}
      <div className="sb-form-generator_files-field-container">
        <FormField
          fieldName={name}
          className="sb-form-generator_files-field"
          readOnly={isReadOnly}
          data-testid={`FormGenerator__FilesField_Field_${field.name}`}
          control={(formMethods) => (
            <Controller
              {...defaultValueProps}
              name={name}
              control={formMethods.control}
              render={({ field: { value, onChange } }) => (
                <FileUploader
                  id={id}
                  testMetadata={{
                    input: {
                      'data-testid': `FormGenerator__FilesField_${field.name}`,
                    },
                    button: {
                      'data-testid': `FormGenerator__FilesField_Button_${field.name}`,
                    },
                    delete: {
                      'data-testid': `FormGenerator__FilesField_Delete_${field.name}`,
                    },
                  }}
                  value={value ? Array.from(value as SBFile[]) : []}
                  isDisabled={isDisabled || uploading}
                  isReadOnly={isReadOnly}
                  displayFiles={
                    field?.layout === 'single' || !field.multiple
                      ? 'string'
                      : 'list'
                  }
                  multiple={field.multiple}
                  description={field.subdescription}
                  accept={field.accept}
                  maxSize={field.fileSize}
                  deleteFileConfirmationModal={
                    field.deleteEntryConfirmationModal
                  }
                  handleFileUpload={async (files, rawFiles) => {
                    const transport = get(
                      context.transport,
                      loadName || field.name,
                    );

                    if (transport?.save) {
                      setUploading(true);

                      // Create a FileList object for the field listener
                      const dt = new DataTransfer();
                      for (let i = 0; i < rawFiles.length; i++) {
                        const file = rawFiles[i];
                        if (
                          files.find(
                            (currentFile) =>
                              file.name === currentFile.name &&
                              !currentFile.error,
                          )
                        ) {
                          dt.items.add(file);
                        }
                      }

                      // Trigger the upload
                      const savedFiles = await transport.save(dt.files);
                      const newValue = [
                        ...(Array.isArray(value) ? value : []),
                        ...savedFiles,
                      ];

                      setUploading(false);
                      onChange(newValue);

                      return savedFiles;
                    }
                  }}
                  handleFileDelete={async (fileToDelete) => {
                    const transport = get(
                      context.transport,
                      loadName || field.name,
                    );
                    if (transport?.delete) {
                      await transport.delete(fileToDelete.name);
                      const newValue = (value as any).filter(
                        (file: SBFile) => file.name !== fileToDelete.name,
                      );

                      onChange(newValue);
                    }
                  }}
                />
              )}
            />
          )}
          fixedHeightFeedback={context.options.display.feedback.reserveSpace}
          customGetErrorMessage={field.customGetErrorMessage}
          customIsErrorValidation={field.customIsErrorValidation}
        />
      </div>
    </div>
  );
};

export default FilesField;
