import { useContext } from 'react';
import classNames from 'classnames';
import { toolbarPlacement } from '../utils';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Styles
import './Header.scss';

const Header = (): JSX.Element | null => {
  const context = useContext(FormGeneratorContext);
  const styles = context.options.styles?.header || {};
  const placement = toolbarPlacement(
    context.currentBreakpoint,
    context.options.toolbar?.location,
  );
  const isSticky = context.options.header?.isSticky;

  if (placement === 'bottom' && context.options.formName === '') {
    return null;
  }

  const Titlebar = context.options.components.titlebar;
  const Toolbar = context.options.components.toolbar;
  return (
    <div
      className={classNames('sb-form-generator_header', {
        'sb-form-generator_header-sticky': isSticky,
      })}
      data-testid="FormGenerator__Component_Header"
      {...context.testMetadata?.header}
      style={styles}
    >
      <Titlebar />
      {placement === 'top' ? <Toolbar /> : ''}
    </div>
  );
};

export default Header;
