import { ElementType, useCallback, useContext } from 'react';
import { FormGeneratorContext } from '../FormGenerator';

const Cancel = (): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const CancelButton = context.options.components.cancel as ElementType;

  const onCancel = useCallback(
    (e) => {
      e.preventDefault();

      if (context.resetOnCancel) {
        context.reset();
      }

      if (context.onCancel) {
        context.onCancel(context.reset);
      }
    },
    [context],
  );

  return (
    <CancelButton
      styleType="demoted"
      sizeType="small"
      data-testid="FormGenerator__Cancel_Button"
      disabled={context.actionRunning}
      onClick={onCancel}
    >
      {context.options.cancelButton ?? 'Reset'}
    </CancelButton>
  );
};

export default Cancel;
