import { useCallback, useContext, useEffect, useMemo, useRef } from 'react';
import classNames from 'classnames';
import { useFieldArray } from 'react-hook-form';
import get from 'lodash/get';

// Components
import Actions from './TableComponents/Actions';
import FieldRow from './TableComponents/FieldRow';
import TableHeader from './TableComponents/TableHeader';
import Typography from '../../Typography';
import { Table, TableBody } from '../../Table';
import TooltipTemplate from '../../TooltipTemplate';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Styles
import './TableRepeater.scss';

// Types
import { BaseFieldType, FieldProps, Fields as FieldsType } from '../types';

// Hooks
import useFieldReadOnly from '../Hooks/useFieldReadOnly';
import useFieldDisabled from '../Hooks/useFieldDisabled';

// Utils
import { getFieldDefault } from '../utils';

const TableRepeater = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  index,
}: FieldProps<I, N>): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  // Setting readOnly at repeater field level will override readOnly at field level as well.
  const isReadOnly = useFieldReadOnly(field);
  const isDisabled = useFieldDisabled(field);
  const { fields, append, remove } = useFieldArray({
    control: context.control,
    keyName: 'repeaterId',
    name: name, // unique name for your Field Array
  });
  const prevFieldLength = useRef(fields.length);
  const settings = context.repeaterSettings[field.name] || {
    mode: 'single',
  };

  const isVisible = context.utils.isVisible(field, index);
  const tableRepeaterClassNames = useMemo(
    () =>
      classNames(
        'sb-form-generator_field',
        'sb-form-generator_tablerepeater',
        {
          'sb-form-generator_field_hidden': !isVisible,
        },
        field.class,
      ),
    [field.class, isVisible],
  );

  const onAdd = useCallback(
    async (e, numberToAdd: number) => {
      e.preventDefault();
      const dataArray = [];
      const transport = get(
        context.transport,
        `${field.name}.createRows`,
        (values: Record<string, unknown>) => values,
      );

      for (let i = 1; i <= numberToAdd; i++) {
        const rowData =
          field.fields &&
          field.fields.reduce((acc, f) => {
            return {
              ...acc,
              [f.name]: getFieldDefault(
                f,
                context.options.fieldTypeDefaultValues,
              ),
            };
          }, {});
        const data = await transport(rowData);
        data && dataArray.push(data);
      }
      dataArray.length > 0 && append(dataArray);
    },
    [append, field.fields, field.name, context],
  );

  const onDelete = useCallback(
    (e, index: number) => {
      e.preventDefault();
      remove(index);
    },
    [remove],
  );

  useEffect(() => {
    prevFieldLength.current = fields.length;
  }, [fields.length]);

  return (
    <div
      className={tableRepeaterClassNames}
      data-testid={`FormGenerator__TableRepeaterField_${field.name}`}
    >
      {field.label && (
        <div
          className="sb-form-generator_tablerepeater-header"
          data-testid="FormGenerator__TableRepeaterField_Header"
        >
          <div className="sb-form-generator_tablerepeater-header_title">
            <Typography
              variant="T40"
              className="sb-form-generator_label sb-form-generator_tablerepeater-label"
            >
              {field.label}
              {field.tooltip && (
                <TooltipTemplate
                  tooltipContent={field.tooltip}
                  size="small"
                  testMetadata={{
                    icon: {
                      'data-testid': `FormGenerator__TableRepeaterField_Tooltip_${field.name}`,
                    },
                  }}
                />
              )}
              {field.rowRequired && !isReadOnly && (
                <span className="sb-form-generator_tablerepeater-required">
                  *Required
                </span>
              )}
            </Typography>
            {field.actions && !isReadOnly && <Actions field={field} />}
          </div>
          {!isReadOnly && field.description && (
            <Typography
              variant="T15"
              className="sb-form-generator_description sb-form-generator_tablerepeater-description"
            >
              {field.description}
            </Typography>
          )}
        </div>
      )}
      {field.fields && (
        <Table
          wrapperClassName="sb-form-generator_tablerepeater-wrapper"
          className="sb-form-generator_tablerepeater-table"
          data-testid="FormGenerator__TableRepeaterField_Table"
        >
          <TableHeader
            field={field}
            fields={field.fields}
            onAdd={onAdd}
            readOnly={isReadOnly}
            disabled={isDisabled}
          />
          <TableBody className="sb-form-generator_tablerepeater-table_body">
            {fields &&
              settings.mode !== 'bulk' &&
              fields.map((item, index) => {
                return (
                  <FieldRow
                    item={item}
                    itemIndex={index}
                    fields={field.fields as FieldsType<I, N>}
                    key={item.repeaterId}
                    name={field.name}
                    clearable={
                      (!field.rowRequired || fields.length > 1) &&
                      field.clearable &&
                      !isDisabled
                    }
                    onDelete={onDelete}
                    readOnly={isReadOnly}
                    isDeleting={
                      fields.length < prevFieldLength.current ? true : false
                    }
                  />
                );
              })}
            {fields && settings.mode === 'bulk' && (
              <FieldRow
                bulk={fields.length}
                itemIndex={0}
                fields={field.fields as FieldsType<I, N>}
                name={field.name}
                clearable={field.clearable && !isDisabled}
                onDelete={onDelete}
                readOnly={isReadOnly}
                isDeleting={
                  fields.length < prevFieldLength.current ? true : false
                }
              />
            )}
          </TableBody>
        </Table>
      )}
    </div>
  );
};

export default TableRepeater;
