import { BaseFieldType, FormSchema } from './types';
import { parseFormSchema } from './utils';

const mergeUtility = <I extends Record<string, unknown>, N = BaseFieldType>(
  coreSchema: FormSchema<I, N> | string,
  ...subSchemas: (Pick<FormSchema<I, N>, 'pages' | 'fields'> | string)[]
): FormSchema<I, N> => {
  const finalSchema = {
    ...(typeof coreSchema === 'string'
      ? parseFormSchema<I, N>(coreSchema)
      : coreSchema),
  };

  subSchemas.forEach((subSchema) => {
    const parsedSchema =
      typeof subSchema === 'string'
        ? parseFormSchema<I, N>(subSchema)
        : subSchema;
    if (parsedSchema.pages) {
      finalSchema.pages = [...(finalSchema.pages || []), ...parsedSchema.pages];
    }
    if (parsedSchema.fields) {
      finalSchema.fields = [
        ...(finalSchema.fields || []),
        ...parsedSchema.fields,
      ];
    }
  });

  return finalSchema;
};

export default mergeUtility;
