import { useContext } from 'react';
import get from 'lodash/get';
import { useFormContext } from 'react-hook-form';

// Components
import Input from '../../Input';
import Typography from '../../Typography';

// Context
import { FormGeneratorContext } from '../FormGenerator';

// Types
import { BaseFieldType, FieldProps } from '../types';

const DisplayField = <I extends Record<string, unknown>, N = BaseFieldType>({
  field,
  name,
  defaultValue,
}: FieldProps<I, N>): JSX.Element => {
  const { ...methods } = useFormContext<I>();
  const context = useContext(FormGeneratorContext);
  const displayValue = get(context.values, name);

  // Passing undefined to defaultValue appears to clear certain fields. Using this method, we avoid passing the prop at all if it's not needed
  const defaultValueProps = {
    ...(defaultValue && { defaultValue: defaultValue as string }),
  };

  const { ref, ...registerProps } = methods.register(name);
  return (
    <Typography variant="T15" className="sb-form-generator_key">
      {displayValue}
      <Input
        type="hidden"
        inputRef={ref}
        {...registerProps}
        data-testid={`FormGenerator__DisplayField_${field.name}`}
        {...defaultValueProps}
      />
    </Typography>
  );
};

export default DisplayField;
