import { useMemo } from 'react';
import get from 'lodash/get';

// Types
import { Field, FormContext, FormFieldValue } from '../types';
import { FieldValues } from 'react-hook-form';

export const useWatchFields = <I extends Record<string, unknown>>(
  formValues: FormContext<I>['values'],
  watch?: Field<I>['watch'],
  index?: number,
): FieldValues | unknown | null => {
  return useMemo(() => {
    if (typeof watch === 'string') {
      const watchedField =
        index !== undefined ? watch.replace('[i]', `[${index}]`) : watch;
      return get(formValues, watchedField) || null;
    }

    if (Array.isArray(watch)) {
      const fieldsToWatch: FormFieldValue[] = [];
      watch.forEach((watch) => {
        const watchedField =
          index !== undefined ? watch.replace('[i]', `[${index}]`) : watch;
        const fieldValue = get(
          formValues,
          watchedField,
        ) as unknown as FormFieldValue;
        if (fieldValue) {
          fieldsToWatch.push(fieldValue);
        }
      });
      return fieldsToWatch.length > 0 ? fieldsToWatch : null;
    }

    return null;
  }, [watch, formValues, index]);
};

export default useWatchFields;
