import { SyntheticEvent } from 'react';

import AddButton from '../../../AddButton';

const FormAddButton = ({
  onClick,
  className,
  ...otherProps
}: {
  onClick: (e: SyntheticEvent) => void;
  className: string;
  'data-testid': string;
  disabled?: boolean;
}): JSX.Element => {
  return (
    <AddButton
      className={className}
      data-testid={otherProps['data-testid']}
      sizeType="small"
      onClick={onClick}
      alt="Add Row"
      title="Add Row"
      disabled={otherProps.disabled}
    />
  );
};

export default FormAddButton;
