import { Control, Controller, RegisterOptions, Path } from 'react-hook-form';

// Components
import FormField, { FormFieldProps } from '../FormField';
import Toggle, { ToggleProps } from '../Toggle';
import FormReadOnlyFieldBooleanValue from '../FormReadOnlyFieldBooleanValue';

// Types
import { TestMetadata } from 'types';

export interface FormToggleFieldProps<I extends Record<string, unknown>> {
  /** Name for field (Unique in the form) */
  fieldName: Path<I>;
  /** Display name for field */
  labelText?: string;
  /** Tooltip icon and hover text */
  tooltip?: string[];
  /** Label style for the toggle */
  labelStyle?: boolean;
  /** Mark field required (*) */
  required?: boolean;
  /** Rules to pass down to control */
  rules?: RegisterOptions;
  /** Additional input props for Input, based on the Input Component */
  inputProps?: Partial<ToggleProps>;
  /** Sets helper */
  helper?: string;
  /** Class name */
  className?: string;
  /** Sets test id */
  testMetadata?: {
    wrapper?: TestMetadata;
    input?: TestMetadata;
  };
  defaultValue?: boolean;
}

const FormToggleField = <I extends Record<string, unknown>>({
  fieldName,
  labelText,
  tooltip,
  required,
  testMetadata,
  rules,
  helper,
  inputProps,
  readOnly,
  defaultValue,
  ...otherProps
}: FormToggleFieldProps<I> & Partial<FormFieldProps<I>>): JSX.Element => {
  return (
    <FormField<I>
      labelText={labelText}
      tooltip={tooltip}
      fieldName={fieldName}
      required={required}
      helper={helper}
      readOnly={readOnly}
      {...testMetadata?.wrapper}
      control={(formMethods, props) => (
        <Controller
          name={fieldName as string}
          defaultValue={defaultValue !== undefined ? defaultValue : false}
          control={formMethods.control as Control<Record<string, unknown>>}
          rules={{
            ...rules,
          }}
          render={({ field: { onChange, value } }) => {
            if (readOnly) {
              return (
                <FormReadOnlyFieldBooleanValue
                  value={value as boolean}
                  testMetadata={testMetadata?.input}
                />
              );
            }

            return (
              <Toggle
                className={props.className}
                switchName={fieldName as string}
                onChange={() => {
                  onChange(!value);
                }}
                checked={value as boolean}
                {...inputProps}
                inputProps={{
                  ...testMetadata?.input,
                  ...inputProps?.inputProps,
                }}
              />
            );
          }}
        />
      )}
      {...otherProps}
    />
  );
};

export default FormToggleField;
