import { useContext } from 'react';

// Context
import { FormGeneratorContext } from '../../FormGenerator';

// Types
import {
  Action as ActionType,
  BaseFieldType,
  Field as FieldType,
} from '../../types';

const Action = <I extends Record<string, unknown>, N = BaseFieldType>({
  action,
  field,
}: {
  action: ActionType<I, N>;
  field: FieldType<I, N>;
}): JSX.Element => {
  const context = useContext(FormGeneratorContext);
  const ActionType = context.options.actions[action.type];
  if (!ActionType) {
    return <></>;
  }
  return <ActionType field={field} action={action} />;
};

export default Action;
