/**
 * Check form toolbar schema type to support backward compatibility with old api
 * toolbar: string vs new api toolbar: ToolbarSchema
 * @param toolbar
 */
const isToolbarSchema = (
  toolbar?: ToolbarSchema | ToolbarPlacement | ToolbarPlacementSettings,
): toolbar is ToolbarSchema =>
  (toolbar as ToolbarSchema)?.actions !== undefined ||
  (toolbar as ToolbarSchema)?.location !== undefined;

const parseOptions = <I extends Record<string, unknown>, N = BaseFieldType>(
  form: FormSchema<I, N>,
  customValidators?: Validator<I, N>,
  customFields?: CustomFieldType,
  customFieldDefaultValues?: CustomFieldDefaultValueType,
  customComponents?: CustomComponents,
  customActions?: CustomActionType,
  customPortals?: CustomPortalType,
  customToolbarActions?: ToolbarActionType,
): FormOptions<I, N> => {
  return {
    display: {
      ...form.display,
      feedback: {
        ...form.display?.feedback,
        reserveSpace:
          typeof form.display?.feedback?.reserveSpace !== 'undefined'
            ? form.display?.feedback?.reserveSpace
            : true,
      },
    },
    labels: form?.labels || {},
    styles: form?.styles || {},
    pages: form?.pages || false,
    visibleFields: form?.visibleFields || undefined,
    enabledFields: form?.enabledFields || undefined,
    defaultValues: (form?.defaultValues as DeepPartial<I>) || undefined,
    defaultFieldSize: {
      slim: 6,
      small: 12,
      medium: 12,
      large: 12,
      ...form?.defaultFieldSize,
    },
    portals: {
      ...customPortals,
    },
    form: {
      ...form,
      confirmation: {
        success: form?.confirmation?.success
          ? form.confirmation.success
          : false,
        cancel: form?.confirmation?.cancel ? form.confirmation.cancel : false,
      },
      pagination: {
        hideBreakpoints: form?.pagination?.hideBreakpoints
          ? form.pagination.hideBreakpoints
          : ['slim'],
      },
    },
    formName: form?.name || '',
    header: {
      isSticky: form.header?.isSticky,
      areTabsSticky: form.header?.areTabsSticky,
    },
    footer: {
      isSticky: form.footer?.isSticky,
    },
    toolbar: {
      location: isToolbarSchema(form.toolbar)
        ? form.toolbar?.location ?? 'top'
        : form.toolbar || 'top',
      actions:
        isToolbarSchema(form.toolbar) && form.toolbar?.actions
          ? form.toolbar?.actions
          : ['cancel', 'submit', 'edit'],
      hideCleanCancel:
        isToolbarSchema(form.toolbar) && form.toolbar?.hideCleanCancel,
    },
    formDescription: form?.description || '',
    saveButton: form?.saveButton || 'Save',
    cancelButton: form?.cancelButton ?? 'Reset',
    saveDraftButton: form?.saveDraftButton ?? 'Save Draft',
    components: {
      header: Header,
      footer: Footer,
      titlebar: FormTitlebar,
      toolbar: FormToolbar,
      headerWrapper: ComponentWrapper,
      footerWrapper: ComponentWrapper,
      bodyWrapper: ComponentWrapper,
      contentWrapper: ComponentWrapper,
      repeaterDelete: FormDeleteButton,
      repeaterAdd: FormAddButton,
      cancel: FormCancelButton,
      saveDraft: FormSaveDraftButton,
      submit: FormSubmitButton,
      ...customComponents,
    },
    fieldTypes: {
      section: Section,
      input: TextField,
      inputmask: MaskField,
      number: NumberField,
      select: SelectField,
      multiselect: MultiSelect,
      date: DateInput,
      daterange: DateRangeField,
      checkbox: CheckboxField,
      radio: RadioField,
      markdown: MarkdownField,
      textarea: TextArea,
      toggle: ToggleField,
      toggleswitch: ToggleSwitchField,
      hidden: HiddenField,
      multiselectpopout: MultiSelectPopoutField,
      tablerepeater: TableRepeater,
      display: DisplayField,
      filemanager: FilesField,
      upload: UploadField,
      readonly: ReadOnlyField,
      ...customFields,
    },
    fieldTypeDefaultValues: {
      ...customFieldDefaultValues,
    },
    actions: {
      bulk: Bulk,
      ...customActions,
    },
    validators: {
      pattern: pattern,
      required: required,
      min: min,
      max: max,
      ...customValidators,
    },
    customToolbarActions: {
      cancel: Cancel,
      edit: Edit,
      save: SaveDraft,
      submit: Submit,
      ...customToolbarActions,
    },
    drafts: form?.drafts,
  };
};

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const FormGeneratorContext = createContext<Record<string, any>>({});

export interface FormGeneratorProps<
  I extends Record<string, unknown>,
  N = BaseFieldType,
> {
  /** ID of the form element (should be unique) */
  formId: string;
  /** Form Schema (JSON or JS Object) */
  form: FormSchema<I, N>;
  /** Handle submit */
  onSubmit: FormGeneratorSubmit<I>;
  /** Handle error */
  onError?: SubmitErrorHandler<I>;
  /** Handle cancel */
  onCancel?: (reset: () => void) => void;
  /** onCancel callback reset form values  */
  resetOnCancel?: boolean;
  /** Custom Validators */
  validators?: Validator<I, N>;
  /** Custom Fields */
  fieldTypes?: CustomFieldType;
  /** Custom Field Default Values */
  fieldTypeDefaultValues?: CustomFieldDefaultValueType;
  /** Custom Actions */
  actions?: CustomActionType;
  /** Override Internal Components */
  customComponents?: CustomComponents;
  /** Container props */
  containerProps?: Omit<ContainerProps, 'children'>;
  /** Functions for loading field data asynchronously */
  loadData?: Record<
    keyof I,
    LoadDataFunc<I> | Record<keyof I, LoadDataFunc<I>>
  >;
  transport?: Record<
    keyof I,
    AsyncTransportFunc<I> | Record<keyof I, AsyncTransportFunc<I>>
  >;
  /** How the grid should behave */
  gridMode?: GridMode;
  /** Test data */
  testMetadata?: {
    form?: TestMetadata;
    header?: TestMetadata;
    pagination?: TestMetadata;
    grid?: TestMetadata;
    footer?: TestMetadata;
    toolbar?: TestMetadata;
    titlebar?: TestMetadata;
  };
  // React-Hook-Form validationMode
  validationMode?: Mode;
  // React-Hook-Form revalidationMode
  revalidationMode?: Exclude<Mode, 'onTouched' | 'all'>;
  // Triggers when the form state isDirty flag changes
  handleUnsaved?: (isDirty: boolean) => void;
  // Locked status
  locked?: Locked;
  // Key value pair of portal and what portal to use
  portals?: CustomPortalType;
  // Whether the form can be submitted - button is enabled
  isSubmittable?: boolean;
  /** Handle saving draft */
  onSaveDraft?: FormGeneratorSubmit<I>;
  /** Custom Toolbar Actions */
  customToolbarActions?: ToolbarActionType;
  /** Additional event information to pass to analytics tracking */
  analyticsEventData?: TAnalyticsEventData;
}

const FormGenerator = <I extends Record<string, unknown>, N = BaseFieldType>({
  formId,
  form,
  onSubmit,
  onError,
  onCancel,
  resetOnCancel = true,
  validators,
  fieldTypes,
  fieldTypeDefaultValues,
  actions,
  customComponents,
  customToolbarActions,
  containerProps,
  testMetadata = { form: { 'data-testid': 'FormGenerator__Form' } },
  loadData,
  validationMode = 'onSubmit',
  revalidationMode = 'onChange',
  gridMode = 'viewport',
  handleUnsaved,
  locked,
  portals,
  isSubmittable = true,
  onSaveDraft,
  transport,
  analyticsEventData,
}: FormGeneratorProps<I, N>): JSX.Element => {
  const [formLocked, setFormLocked] = useState(locked?.initial === true);
  const didMount = useRef(false); // useRef lets us skip a re-render when setting this
  const savingDraftRef = useRef(false);
  const getFormValuesRef = useRef<UseFormGetValues<I>>();
  const schema = parseFormSchema<I, N>(form);
  const schemaHash = JSON.stringify(schema);

  const options = useMemo(
    () =>
      parseOptions<I, N>(
        schema,
        validators,
        fieldTypes,
        fieldTypeDefaultValues,
        customComponents,
        actions,
        portals,
        customToolbarActions,
      ),
    // We want to use the string version of form for more accurate nested dependency checks
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [customComponents, fieldTypes, validators, schemaHash, actions, portals],
  );

  const [currentPage, setCurrentPage] = useState(
    (options?.pages && options.pages[0].name) || false,
  );

  const [tableSettings, setTableSettings] = useState({});

  const [screenSize, setScreenSize] = useState<FormGeneratorSize | undefined>();

  const breakpoint =
    gridMode === 'slim'
      ? 'slim'
      : screenSize
      ? getBreakpoint(screenSize, gridMode === 'container')
      : '';

  const containerRef = useRef<HTMLDivElement>(null);

  const { fieldRef, fieldMap, defaultValues, validationSchema } =
    useSchemaState(schema, options);

  const methods = useForm<I>({
    resolver: yupResolver(validationSchema),
    defaultValues,
    mode: validationMode,
    reValidateMode: revalidationMode,
    context: {
      currentPage,
      options,
      screenSize,
      breakpoint,
      gridMode,
      savingDraftRef,
      getFormValuesRef,
    },
  });

  /*
   Provide a way to get any form values in the context of yup validation.
   Note: `this.parent` only goes one level up in the schema and in the context of table repeater that is not enough.
  */
  getFormValuesRef.current = methods.getValues;

  /*Confirmation onCancel Setup*/
  const [isConfirmCancelModalOpen, setIsConfirmCancelModalOpen] =
    useState<boolean>(false);
  const onConfirmedCancel = (): void => {
    resetOnCancel && methods.reset();
    onCancel && onCancel(methods.reset);
    setIsConfirmCancelModalOpen(false);
  };

  const FooterBar = options.components.footer as ElementType;
  const HeaderBar = options.components.header as ElementType;
  const HeaderWrapper = options.components.headerWrapper as ElementType;
  const FooterWrapper = options.components.footerWrapper as ElementType;
  const BodyWrapper = options.components.bodyWrapper as ElementType;
  const ContentWrapper = options.components.contentWrapper as ElementType;

  const formValues = methods.watch();

  const calculateSizes = useCallback(
    (field) => {
      return calculateFieldSizes<I, N>(field, options, gridMode, screenSize);
    },
    [options, gridMode, screenSize],
  );

  const resetTableSettings = useCallback(() => {
    setTableSettings({});
  }, []);

  const configureTable = useCallback(
    (field: Field<I, N>, settings, reset?: boolean) => {
      if (field.name) {
        // Save a tables settings
        setTableSettings({
          ...tableSettings,
          [field.name]: settings,
        });
        // Reset useFieldArray when settings change
        reset &&
          methods.reset({
            ...(formValues as DeepPartial<I>),
            [field.name]: (defaultValues && defaultValues[field.name]) || [],
          });
      }
    },
    [tableSettings, setTableSettings, methods, formValues, defaultValues],
  );

  // Handles backwards compatibility or data massaging for the final submission
  const preprocessSubmission = useCallback(
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    (data: DeepPartial<any>) => {
      const submitData = cloneDeep(data);
      fieldMap.forEach((field) => {
        // Adds backwards compatibility to ensure empty arrays are returned for empty field arrays - which is improper but what we've been doing.
        // https://github.com/react-hook-form/react-hook-form/discussions/1886
        if (field.type === 'tablerepeater' && !submitData[field.name]) {
          submitData[field.name] = [];
        }
        // Preserve old format for bulk mode
        if (
          field.type === 'tablerepeater' &&
          submitData?.[field.name]?.[0]?.bulk
        ) {
          const displayField = field?.fields?.find(
            (childField) => childField.type === 'display',
          );
          submitData[field.name] = [submitData?.[field.name]?.[0]];

          displayField &&
            delete submitData?.[field.name]?.[0]?.[displayField.name];
        }
      });
      return submitData;
    },
    [fieldMap],
  );

  const analyticsContext = useContext(AnalyticsContext);

  const processSubmission: SubmitHandler<I> = useCallback(
    async (data) => {
      const submitData = preprocessSubmission(data);
      const fieldContext = getFieldContext(submitData, fieldRef);

      if (savingDraftRef.current && onSaveDraft) {
        await onSaveDraft(submitData, {
          ...fieldContext,
          dirtyFields: methods.formState.dirtyFields as DeepMap<
            DeepPartial<I>,
            boolean
          >,
        });
        handleAnalyticsEvent({
          analyticsContext,
          analyticsEventData,
          componentType: 'Form',
          actionName: 'saveDraft',
          label: formId || '',
          value: [methods.getValues()],
        });
      } else {
        await onSubmit(submitData, {
          ...fieldContext,
          dirtyFields: methods.formState.dirtyFields as DeepMap<
            DeepPartial<I>,
            boolean
          >,
        });
        methods.reset(methods.getValues(), {
          keepValues: true,
          keepDefaultValues: true,
          keepErrors: true,
          keepIsValid: true,
        });
        handleAnalyticsEvent({
          analyticsContext,
          analyticsEventData,
          componentType: 'Form',
          actionName: 'submit',
          label: formId || '',
          value: [methods.getValues()],
        });
      }
    },
    [
      preprocessSubmission,
      fieldRef,
      onSaveDraft,
      onSubmit,
      analyticsContext,
      formId,
      analyticsEventData,
      methods,
    ],
  );

  const isVisible = useCallback(
    (field, index) => {
      return showField(
        field,
        currentPage,
        options,
        formValues,
        gridMode,
        screenSize,
        index,
      );
    },
    [currentPage, options, formValues, gridMode, screenSize],
  );

  const isEnabled = useCallback(
    (field: Field<I>, index) => {
      if (formLocked) {
        return false;
      } else if (options.enabledFields) {
        return (
          options.enabledFields.includes(field.name) &&
          fieldEnabled(field, formValues, index)
        );
      } else {
        return fieldEnabled(field, formValues, index);
      }
    },
    [formLocked, formValues, options],
  );

  const isEditable = useCallback(
    (field: Field<I>, index) => {
      if (formLocked && locked?.readOnly) return false;
      else return fieldEditable(field, formValues, index);
    },
    [formValues, formLocked, locked?.readOnly],
  );

  /*Confirmation Message Setup*/
  const [isConfirmModalOpen, setIsConfirmModalOpen] = useState<boolean>(false);
  const onConfirmedSubmit = (): void => {
    methods.handleSubmit(processSubmission, onError)();
    setIsConfirmModalOpen(false);
  };

  const getMenuPortalTarget = useCallback(
    (field) => {
      // Inline does not need menu portal target set
      if (field.layout === 'inline' || typeof window === 'undefined') {
        return null;
      }

      // User indicated they want to specify a portal
      if (field.portal && options.portals[field.portal]) {
        return options.portals[field.portal];
      }

      // Otherwise use document body
      return document.body;
    },
    [options],
  );

  const handleResize = (): void => {
    const { clientWidth, clientHeight } = document.documentElement;
    const containerWidth = containerRef.current
      ? containerRef.current.clientWidth
      : 0;
    const containerHeight = containerRef.current
      ? containerRef.current.clientHeight
      : 0;

    setScreenSize({
      viewport: {
        innerHeight: clientHeight,
        innerWidth: clientWidth,
      },
      container: {
        innerHeight: containerHeight,
        innerWidth: containerWidth,
      },
    });
  };

  useEffect(() => {
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    // Only call after the first render (isDirty is always false to start)
    if (didMount.current) {
      handleUnsaved?.(methods.formState.isDirty);
    }
  }, [handleUnsaved, methods.formState.isDirty]);

  useEffect(() => {
    if (didMount.current) {
      methods.reset(defaultValues);
      resetTableSettings();
      setFormLocked(locked?.initial === true);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [schemaHash]);

  const setSavingDraft = useCallback(
    (savingDraft: boolean) => {
      savingDraftRef.current = savingDraft;
    },
    [savingDraftRef],
  );

  const formClassNames = useMemo(
    () =>
      classNames('sb-form-generator_form', {
        'sb-form-generator_grid-mode_viewport': gridMode === 'viewport',
        'sb-form-generator_grid-mode_container': gridMode === 'container',
        'sb-form-generator_grid-mode_slim': gridMode === 'slim',
        'sb-form-generator_grid-breakpoint_slim':
          screenSize &&
          ((gridMode === 'viewport' && getBreakpoint(screenSize) === 'slim') ||
            (gridMode === 'container' &&
              getBreakpoint(screenSize, true) === 'slim') ||
            gridMode === 'slim'),
      }),
    [gridMode, screenSize],
  );

  // useEffect called in order, must come last
  // Sets didMount so that future useEffects know the first render is complete
  useEffect(() => {
    didMount.current = true;
  }, []);

  // This does a deep diff on defaultValues and current values to detect issues with bad or invalid data.
  useEffect(() => {
    if (
      didMount.current &&
      methods.formState.isDirty &&
      Object.keys(methods.formState.dirtyFields).length === 0 &&
      defaultValues
    ) {
      console.group('FormGenerator:ParseError');
      console.warn('There may be invalid default field values.');
      const currentValues = methods.getValues();
      const diff = Object.keys(defaultValues)
        .sort()
        .reduce((diff, key) => {
          if (isEqual(defaultValues[key], currentValues[key])) return diff;
          return {
            ...diff,
            [key]: {
              default: defaultValues[key],
              current: currentValues[key],
            },
          };
        }, {});
      console.log('FormGenerator:FieldComparison');
      console.log(diff);
      console.groupEnd();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [methods.formState.isDirty, methods.formState.dirtyFields, defaultValues]);

  return (
    <Container {...containerProps} containerRef={containerRef}>
      {isConfirmModalOpen && (
        <ConfirmationModal
          title={form?.confirmation?.successModal?.title || 'Confirm Submit'}
          text={
            form?.confirmation?.success &&
            typeof form.confirmation.success === 'string'
              ? form.confirmation.success
              : 'Are you sure you want to submit?'
          }
          messageType={form?.confirmation?.successModal?.messageType}
          acceptButtonText={form?.confirmation?.successModal?.acceptButtonText}
          cancelButtonText={form?.confirmation?.successModal?.cancelButtonText}
          onConfirm={onConfirmedSubmit}
          onClose={() => {
            setIsConfirmModalOpen(false);
          }}
          bodyOverlay="blackout"
        />
      )}
      {isConfirmCancelModalOpen && (
        <ConfirmationModal
          title={
            form?.confirmation?.cancelModal?.title ||
            `Confirm ${resetOnCancel ? 'Reset' : 'Cancel'}`
          }
          text={
            form?.confirmation?.cancel &&
            typeof form.confirmation.cancel === 'string'
              ? form.confirmation.cancel
              : 'You have pending changes. Are you sure you want to proceed?'
          }
          messageType={form?.confirmation?.cancelModal?.messageType}
          acceptButtonText={form?.confirmation?.cancelModal?.acceptButtonText}
          cancelButtonText={form?.confirmation?.cancelModal?.cancelButtonText}
          onConfirm={onConfirmedCancel}
          onClose={() => {
            setIsConfirmCancelModalOpen(false);
          }}
          bodyOverlay="blackout"
        />
      )}
      <FormGeneratorContext.Provider
        value={{
          hash: schemaHash,
          form: schema,
          formId: formId,
          options: options,
          fieldRef: fieldRef,
          fieldMap: fieldMap,
          defaultValues: defaultValues,
          validationSchema: validationSchema,
          utils: {
            calculateSizes: calculateSizes,
            isVisible: isVisible,
            isEditable: isEditable,
            isEnabled: isEnabled,
            setCurrentPage: setCurrentPage,
            handleResize: handleResize,
            configureTable: configureTable,
            getMenuPortalTarget: getMenuPortalTarget,
          },
          values: formValues,
          currentPage: currentPage,
          isDirty: methods.formState.isDirty,
          isValid: methods.formState.isValid,
          dirtyFields: methods.formState.dirtyFields,
          errors: methods.formState.errors,
          testMetadata: testMetadata,
          loadData: loadData,
          transport: transport,
          onCancel:
            form.confirmation?.cancel && methods.formState.isDirty
              ? () => setIsConfirmCancelModalOpen(true)
              : onCancel,
          formLocked: formLocked,
          formUnlockable: locked && locked.unlockable !== false,
          setFormLocked: setFormLocked,
          resetOnCancel: resetOnCancel,
          reset:
            form.confirmation?.cancel && methods.formState.isDirty
              ? () => setIsConfirmCancelModalOpen(true)
              : methods.reset,
          actionRunning:
            methods.formState.isSubmitting &&
            (savingDraftRef.current ? 'saveDraft' : 'submit'),
          isSubmittable: isSubmittable,
          setSavingDraft: setSavingDraft,
          onSaveDraft: onSaveDraft,
          screenSize: screenSize,
          gridMode: gridMode,
          control: methods.control,
          getValues: methods.getValues,
          setValue: methods.setValue,
          watch: methods.watch,
          repeaterSettings: tableSettings,
          revalidationMode: revalidationMode,
          currentBreakpoint: breakpoint,
          hideCleanCancel:
            !methods.formState.isDirty &&
            isToolbarSchema(form.toolbar) &&
            form.toolbar?.hideCleanCancel,
        }}
      >
        <FormProvider {...methods}>
          <form
            id={formId}
            onSubmit={
              form?.confirmation?.success
                ? methods.handleSubmit(() => {
                    setIsConfirmModalOpen(true);
                  }, onError)
                : methods.handleSubmit(processSubmission, onError)
            }
            className={formClassNames}
            {...testMetadata?.form}
          >
            <ContentWrapper>
              <HeaderWrapper>
                <HeaderBar />
              </HeaderWrapper>
              <BodyWrapper>
                {options.pages && <Pages />}
                <Grid
                  density="simple"
                  {...testMetadata?.grid}
                  className="sb-form-generator_body"
                >
                  <Fields<I, N> fields={schema.fields} />
                </Grid>
              </BodyWrapper>
              <FooterWrapper>
                <FooterBar />
              </FooterWrapper>
            </ContentWrapper>
          </form>
        </FormProvider>
      </FormGeneratorContext.Provider>
    </Container>
  );
};

export default FormGenerator;
